.exchange-integration-page {
    @import '../../../sass/shared/_variable.scss';
    @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
    *{
        box-sizing: border-box;
        font-family: 'Mukta', sans-serif;
    }
    .showimg {
        display: block;
    }

    .hideimg {
        display: none;
    }

    .mascot-img {
        width: 60px;
        height: 60px;
    }

    .my-account-link {
        color: $purple;
        font-weight: bold;
    }

    .text-purple {
        color: $purple  !important;
    }

    .text-orange {
        color: $orange;
    }

    .crop {
        height: 370px;

        img {
            width: 100%;
            overflow: hidden;
        }
    }

    .banner-content {
        padding-left: 60px;
        padding-right: 20px;

        .banner-heading {
            font-size: $bigger-head;
            color: $purple;
            font-weight: bold;
            line-height: 53px;
        }

        .banner-para {
            font-size: $big-text;
            line-height: 30px;
            padding-right: 50px;
        }

        .btn-bg {
            color: $white;
            padding: 5px 18px;
            background: $orange;
            border-radius: 5px;
            margin-top: 20px;
            line-height: 30px;
            font-weight: 600;
        }

        .btn-align-content {
            text-align: left;
        }

    }

    .text-bold {
        font-weight: bold;
        color: $black;
    }

    .text-underline {
        color: $purple;
    }

    .faq-link {
        color: $purple;
        font-weight: 600;
    }

    .faq-no-link {
        color: $black;
        text-decoration: none;
    }

    .main-content-heading {
        font-weight: bold;
        font-size: $header;
        text-transform: capitalize !important;
    }

    .text-sub-heading {
        font-size: $subheader;
        font-weight: bold;
        line-height: 36px;
    }

    .content-para {
        font-size: $big-text;
        line-height: 30px;
    }

    .content-order-list {
        list-style: none;
    }

    .content-order-list li {
        word-spacing: 2px;
        line-height: 30px;
        font-size: $big-text;

        &::before {
            content: "\2022";
            color: $orange;
            font-weight: bold;
            display: inline-block;
            font-size: $subheader;
            width: 1rem;
            margin-left: -1rem;
        }
    }


    .list-img {
        height: 15px;
        width: 15px;
        margin-right: 10px;
        margin-top: -2px;
    }

    .list-content-span {
        font-size: $subheader;
        line-height: 30px;
    }

    .list-content {
        font-size: $subheader;
        font-weight: normal;
        line-height: 30px;
    }

    .contactus-link {
        color: $purple;
    }

    .text-list {
        a {
            text-align: center !important;
        }
    }

    @media(max-width:1200px) {
        .banner-content {
            padding: 0px 20px;

            .banner-heading {
                font-size: $big-header+10px;
                color: $purple;
                font-weight: bold;
                line-height: 50px;
            }
        }

        .main-content-heading {
            font-weight: bold;
            font-size: $semi-big-header;
        }
    }

    @media(max-width:992px) {
        .banner-content {
            padding-left: 20px;

            .banner-heading {
                font-size: $big-header;
                color: $purple;
                font-weight: bold;
                line-height: 40px;
            }

            .banner-para {
                font-size: $big-text;
                line-height: 30px;
                padding-right: 20px;
            }
        }

        .crop {
            height: 270px;
            text-align: center !important;

            img {
                width: 400px;
                overflow: hidden;

            }
        }
    }

    @media(max-width:767px) {
        .showimg {
            display: none;
        }

        .hideimg {
            display: block;
            text-align: center;
            margin: 10px 0px;
        }

        .banner-content {
            .banner-heading {
                font-size: $big-header;
                font-weight: 600;
                line-height: 41px;
                text-align: left;
            }

            .btn-bg {
                margin-top: 10px;
                margin-bottom: 10px;
            }

            .btn-align-content {
                text-align: center;
            }
        }

        .content-para {
            font-size: $normal-text;
            line-height: 30px;
        }

        .text-sub-heading {
            font-size: $normal-text;
            font-weight: bold;
            line-height: 28px;
        }

        .crop {
            height: 250px;
        }
    }

    @media(max-width:420px) {
        .crop {
            height: 230px;
        }

    }



}