@import '../../../sass/shared/common';

.home-page {
  @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@200&display=swap');

  * {
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
  }

  .portfolio-bg {
    background-color: $home-sliding-bg;
    position: relative;
  }
  .banner-paragraph-margin{
    margin-left: 65px;
  }

  .btn-orange {
    background: $home-btn !important;
    margin-top: 20px;
    border: none;
    padding: 10px 20px;
    color: $white !important;
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;

    &:hover {
      color: $white !important;
      background-color:$home-btn !important;
    }
    &:active {
      color: $white !important;
      background-color:$home-btn !important;
    }

  }

  .portfolio-heading {
    font-size: $bigger-head;
    color: $home-black;
    font-weight: 600;
  }

  .slider-heading {
    margin-bottom: 1rem;
    font-weight: 600;
  }

  .crop-mascot-img {
    width: 300px;
    height: 300px;
  }

  .portfolio-sub-heading {
    font-size: $big-subheader;
    font-weight: 400;
    margin: 10px 0px;
    color: $home-black;
  }

  .portfolio-paragraph {
    font-size: $big-text;
    color: $home-black;
  }

  .home-box-image {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .globe-bg {
    background-color: $globe-bg;
    position: relative;
  }

  span.carousel-control-next-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e");
  }

  span.carousel-control-prev-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e");
  }

  .carousel-control-next-icon,
  .carousel-control-prev-icon {
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 100% 100%;
  }


  .slider-report {
    .slider-report-heading {
      color: $home-black;
      font-size: $header;
      font-weight: 700;

    }

    .slider-report-para {
      padding: 0px 250px !important;
      font-weight: 400;
      font-size: $big-text;
      color: $home-black;
    }
  }

  .content-items {
    top: -30px;
  }

  .banner-section {
    background-position: center;
    background-size: cover;
    margin-top: 57px;
    padding-bottom: 5rem !important;

    .banner-paragraph {
      font-size: $big-subheader;
      margin-bottom: 20px;
      color: $home-black;
    }

    .text-bold {
      font-weight: 500;
    }

    .image-section {
      padding-top: 100px;
    }

    .banner-content-head {
      color: $purple;
      font-size: $head;
      margin-bottom: 50px;
      font-weight: bold;
    }

    .banner-content-para {
      font-size: $big-subheader;
      color: $home-black;
      margin: 0;
      font-weight: 400;
    }

    .para-items {
      font-size: $header;
      color: $banner-para;
      margin-top: 50px;

      .content-imgs {
        margin-right: 20px;
      }
    }

    .banner-content {
      margin-right: 0px;
      padding-right: 0px;
      padding-top: 10px;
    }

  }

  .mobile-display {
    display: none;
  }

  .glabal-col {
    overflow: hidden;
  }

  .desktop-display {
    display: block;
  }

  //  media query for large device
  @media(max-width:1200px) {
    .slider-report {
      .slider-report-para {
        padding: 0px 100px !important;
      }
    }

  }

  //  media query for big-tab device
  @media(max-width:992px) {
    .mobile-display {
      display: block;
    }

    .desktop-display {
      display: none;
    }

    .crop {
      height: 330px;

      img {
        overflow: hidden;
      }
    }

    .crop-mascot {
      height: 250px;

      img {
        overflow: hidden;
      }
    }


    .banner-section {
      text-align: center;

      .image-section {
        text-align: left;
      }
    }

  }

  //  media query for small-tab device
  @media(max-width:767px) {
    .mobile-display {
      display: block;
    }

    .desktop-display {
      display: none;
    }

    .slider-report {
      .slider-report-heading {
        color: $home-black;
        font-size: $big-subheader;
        font-weight: 500;

      }

      .slider-report-para {
        padding: 0px 50px !important;
        font-weight: 400;
        color: $home-black;
      }
    }

    .banner-content-head {
      color: $purple;
      font-size: $big-header !important;
      margin-bottom: 20px;
      font-weight: bold;
      margin-top: -30px !important;
    }

    .portfolio-heading {
      font-size: $header;
    }

    .portfolio-sub-heading {
      font-size: $big-text;
      font-weight: 500;
    }

    .portfolio-paragraph {
      font-size: $normal-text;
    }

    .banner-content-para {
      font-size: $big-text !important;
    }

    .banner-paragraph {
      font-size: $big-text !important;
    }

    .image-section {
      padding-top: 10px !important;
    }

    .para-items {
      margin-top: 20px !important;
    }

    .banner-section {
      padding-bottom: 3rem !important;
    }


  }

  //  media query for small device

  @media(max-width:400px) {
    .content-imgs {
      margin-right: 4px !important;
    }
  }



}