@import '../../shared/common';

.get-exchanges {

    // background: linear-gradient(to bottom, $white, #F8F8F8);
    .exchange-link {
        text-decoration: none;
        color: $home-black;
    }

    .div2-para {
        font-size: $big-header;
        font-weight: bold;

        @media(max-width:991.5px) {
            font-size: 28px;
        }

        span {
            color: $orange;
        }
    }

    .search-div {
        position: relative;
        width: 100%;
        max-width: 15rem;

        @media(min-width:768px) and (max-width:991.5px) {
            max-width: 11rem;
        }

        .input-field {
            padding-right: 2.4rem;
            padding-left: 1rem;
            border-radius: 5px !important;

            &::placeholder {
                font-size: $big-text!important;
            }
        }

        .search-icon {
            position: absolute;
            right: 1rem;
            top: 0.6rem;
            width: 1rem;
            height: auto;
        }
    }

    .div-crypto {

        padding-top: 18px;

        .collapse-crypto {
            overflow: hidden;
            max-height: 28rem;
        }

        .crypto-container {
            text-align: -webkit-center;

            .circle-crypto {
                height: 8.2rem;
                width: 8.2rem;
                border-radius: 5px;
                font-size:$big-text;
                text-align: center;
                word-wrap: break-word;
                background-color: $white;
                margin-bottom: 20px !important;
                border: 1px solid #c7c7c7 !important;
            }
        }

        .crypto-pic {
            margin-top: 1.5rem;
            margin-bottom: 5px;
            width: auto;
            height: 35px;
        }

        .btn-allexchange {
            background-color: $orange;
            color: $white;
            margin-top: 31.2px;
            margin-bottom: 3.2rem;
            font-size: 22px;
            font-weight: 500;
            padding-left: 17px;
            padding-right: 17px;
            border-radius: 5px;

            @media(max-width:991.5px) {
                font-size: $big-text+1px;
            }

            &:hover {
                background-color: $orange;
                color: $white;
            }
        }

        .up-arrow {
            cursor: pointer;
            margin-bottom: 3.2rem;
            margin-top: 31.2px;
        }
    }

    .error-div {
        position: relative;

        .error-para {
            font-size: 32px;
        }

        .retry-btn {
            background: $purple;
            color: $white;
            border: none;
            border-radius: 25px;
            padding: 0.5rem 1.5rem;

            &:hover {
                background: $orange;
            }
        }

        .error-text-div {
            width: 100%;

            @media(min-width:768px) {
                position: absolute;
                top: 6rem;
                right: 2rem;
            }
        }

        .error2-text-div {
            width: 100%;

            @media(min-width:768px) and (max-width:991.5px) {
                position: absolute;
                top: 2rem;
                right: 1rem;
            }

            @media(min-width:992px) {
                position: absolute;
                top: 4rem;
                right: 2rem;
            }

        }

        .error2-para {
            font-size: 24px;
        }

        .back-to-home-btn {
            background: $purple;
            color: $white;
            border: none;
            border-radius: 25px;
            padding: 0.5rem 1.5rem;

            &:hover {
                background: $orange;
            }
        }
    }

    .need-padding {
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .bot-form {
        border-radius: 10px;
        border: 1px solid #707070 !important;
        padding-top: 16px;
        padding-left: 2rem;
        padding-right: 2rem;
        background-color: $white;

        .broken-robot {
            height: 268px;
            width: 242px;
        }
    }

    @media(max-width:767px) {
        .div2-para {
            text-align: center !important;
        }

        .search-div {
            position: relative;
            width: 100%;
            margin: auto;
        }

    }
}