@import "../shared/_common.scss";

.pay-by-crypto {
  @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');

  * {
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
  }

  // DESKTOP VIEW

  .desktop-banner {
    display: block;
  }

  .mobile-banner {
    display: none;
  }

  .text-bold {
    font-weight: bold;
  }

  .content-crypto-mobile {
    display: none;
  }
.content-three-heading{
  color: $orange !important;
}
  .img-content {
    font-size: $big-text;
  }

  .content-crypto {
    position: relative;
  }

  .text-bold {
    font-weight: bold;
  }

  .black-screen {
    background-color:$home-black;
    opacity: 50%;
    width: 100%;
    height: 69.375rem;
    position: absolute;
    animation: blackAnimation 3s forwards ease;
    z-index: +1;
  }

  .text-orange {
    font-weight: bold;
  }


  @keyframes blackAnimation {
    0% {
      opacity: 50%;
      visibility: visible;
    }

    50% {
      opacity: 50%;
      visibility: visible;
    }

    75% {
      opacity: 50%;
      visibility: visible;
    }

    100% {
      opacity: 0;
      display: none;
      visibility: hidden;
    }
  }

  .banner-div {
    position: relative;
    height: 44.875rem;
    width: 100%;

  }

  .launching-soon {

    height: 100%;
    width: 100%;
    background-color: $white;
    animation: showAnimation 3s forwards ease;
    z-index: +2;
  }

  .lets-start {
    position: absolute;
    width: 100%;
    height: 100%;
    animation: hideAnimation 1s forwards;

  }

  @keyframes hideAnimation {
    0% {

      display: none;
    }

    100% {

      display: block;
    }
  }

  @keyframes showAnimation {
    0% {
      opacity: 1;
      visibility: visible;
    }

    50% {
      opacity: 1;
      visibility: visible;
    }

    75% {
      opacity: 1;
      visibility: visible;
    }

    100% {
      opacity: 0;
      display: none;
      visibility: hidden;
    }
  }

  .banner-pay {
    min-height: 100%;
    background: url("../../../public/images/pay-banner-bg.png");
    text-align: center;
    width: 100%;


    .logo-heading-mobile {
      display: none;
    }

    .logo-heading-desktop {
      font-size: $head;
      margin-bottom: 20px !important;
      color: $purple;
      font-weight: 700;
      padding-top: 35px;

      .crptm-logo {
        width: 13.813rem;
        margin: 0;
      }


    }
  }

  .pay-by-crypto-heading {

    .banner2-img-mobile {
      display: none;
    }

    .banner2-img {
      margin: 70px 0px;
    }
  }


  .full-page {
    background-color: $white;
  }

  .content-bg {
    min-height: 12.5rem;
    padding-top: 30px;

    .p-in-content-bg {
      font-size: $big-text;
    }
  }

  .content-one,
  .content-two {
    margin-bottom: 15px;
  }

  .con-col {
    font-size: $big-header;
    font-weight: bold;

  }

  .crypto-bg {

    .crypto-content {
      color: $grey;
      font-size: $big-text;
      text-align: center;
      padding: 0px 0px;
      text-indent: 2px;
      font-weight: 400;
    }

    .i-m-h2 {
      color: $black;
      font-weight: 700;
      font-size: $big-text;
    }
  }

  .hero {
    height: 80vh;
    width: 100%;
    background: $white;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

  }

  .back-img {
    margin-top: 20px;
  }

  .info {
    .transact {
      margin-left: 26.7px;
      font-size: 33px;
      color: $purple;
    }

    .anytime {
      color: $orange;
      line-height: 40px;
    }
  }

  @media (max-width: 992px) {
    .crypto-content {
      font-size: 14px !important;
      text-indent: 0px;

    }

    .import-margin {
      margin-top: 5rem !important;
    }


  }

  // RESPONSIVE VIEW

  @media (max-width: 770px) {

    .con-col {
      font-size: 20px;
    }

    .desktop-banner {
      display: none;
    }

    .mobile-banner {
      display: block;
      height: 856px;
      text-align: center;

      .mob-banner-content {
        height: 522px;
        width: 352px;
      }
    }

    .content-crypto {
      display: none;
    }

    .pay-by-crypto-heading {
      padding-top: 1rem;

      .banner2-img-mobile {
        display: block;
        margin: auto;
        margin-top: 40px;
        margin-bottom: 40px;
      }

      .banner2-img {
        display: none;
      }
    }



    .banner-div {
      height: 40.875rem;

      .banner-pay {
        height: 100%;

        .logo-heading-mobile {
          display: block;
          font-weight: 700;
          font-size: $head-mobile;
          color: $purple;
        }

        .logo-heading-desktop {
          display: none;
        }
      }
    }

    .content-crypto-mobile {
      display: block;


      .content-bg {
        margin: 0;
        width: 100%;
        padding-top: 3rem !important;

        .content-one {
          .con-col {
            font-size: 20px;
            font-weight: 700;
            text-align: center;
            margin: auto;
            margin-bottom: 15px;
          }

          .p-in-content-bg {
            font-size: 18px;
            text-align: center;
            width: 326px;
            height: 190px;
            margin: auto;
            word-spacing: 0;
            letter-spacing: 0;
          }
        }
      }

      .crypto-bg {
        height: 40.25rem;

        .mid-box {
          border: 2px solid $orange;
          border-radius: 28px;

          .content-two-mobile {
            display: block;
            margin-top: 30px;

            .con-col-mobile {
              display: block;
              font-weight: 700;
              width: 287px;
              font-size: 20px;
              line-height: 27.24px;
              text-align: center;
              margin: auto;
              color: $purple;
            }
          }

          .sliding-content {
            margin-top: 36px;
            margin-bottom: 30px;

            .slide {
              height: 420px;

            }
          }
        }
      }

      .feature-bg {
        .content-three {
          font-weight: 700;
          font-size: 24px;
          line-height: 32.68px;
          text-align: center;
          margin: auto;
        }

        .content-img {
          width: 300px;
          height: 312px;
          margin: auto;
          margin-top: 25px;
        }
      }
    }
  }
}