@import "../../shared/common";

.user-portfolio-detail-module {
  .user-portfolio-heading {
    font-size: $header;
    color: $purple;
    font-weight: 600;
  }
  .add-account-btn {
    padding: 5px 18px;
    border-radius: 3rem;
    font-size: $big-text;
    color: $white;
    background-color: $purple;
    &:hover {
      color: $white;
    }
  }
  .details-box {
    background-color: $white;
    border-radius: 10px;
    border: 1px solid $border;
    padding: 1rem;
    height: 13rem;
  }

  .section-heading {
    font-weight: 500;
    color: $purple;
    font-size: $big-text;
    margin: 0px;
    margin-bottom: 0.5rem;
    line-height: 2;
  }

  .details-box .hr-line {
    color: $border;
  }

  .detail-text {
    margin: 1.28rem 0px;
    .portfolio-item {
      font-size: 1rem;
      font-weight: 700;
      line-height: 2;
      color: #222222;
    }
  }

  .account-crypto-img {
    width: 42px;
    height: auto;
  }

  .btn-div {
    text-align: center;
    .btn-app-primary.add-more-btn {
      padding: 5px 18px;
      border-radius: 12px;
      font-size: $normal-text;
      color: $white;
      background-color: $purple;
      border: 1px solid $border;
      margin-top: 0.85rem;
    }
  }
  .user-portfolio-info-icon {
    width: 1rem;
    height: 1rem;
    position: relative;
    top: 0px;
    fill: $tooltip-icon;
    svg{
      fill: $tooltip-icon;
      position: relative;
      top: 0px;
    }
  }
  .account-indicator-failed {
    position: absolute;
    top: 0;
    right: 8px;
    height: 0.8rem;
    width: 0.8rem;
    border-radius: 50%;
    background-color: $failure;
  }

  .account-indicator {
    position: absolute;
    top: 0;
    right: 8px;
    height: 0.8rem;
    width: 0.8rem;
    border-radius: 50%;
    background-color: $success;
  }

  .accounts {
    padding: 0;
    img {
      width: 36px;
      height: 36px;
      padding: 3px;
    }
    border-radius: 50px;
    border: 2px solid $white;
  }
  .account-with-expire {
    padding: 0;
    img {
      width: 36px;
      height: 36px;
      padding: 3px;
    }
    border-radius: 50px;
    border: 2px solid $failure;
  }
  .accounts-with-connected {
    padding: 0;
    img {
      width: 36px;
      height: 36px;
      padding: 3px;
    }
    border-radius: 50px;
    border: 2px solid $success;
  }
  .account-is-synced {
    border-radius: 50px;
    border: 2px solid $success;
  }
  .accounts-array {
    .info-icon {
      padding: 0px 0.5rem;
    }
  }
  .section-box {
    background-color: $white;
    border: 1px solid $border;
    border-radius: 10px;
    padding: 1rem;
  }

  .account-loaders {
    img {
      width: 55px;
      height: auto;
    }
  }
  .rotated-btn {
    transform: rotate(-180deg);
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .non-rotated-btn {
    transform: rotate(0deg);
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .info-table-border {
    border: 1px solid $border;
    border-radius: 7px;
    max-height: 18rem;
    overflow: auto;
  }

  .static-border-bottom {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    background-color: $purple;
    color: $white;
  }

  th.border-adjustment {
    border-color: $white !important;
    padding: 0.5rem !important;
    font-size: $text;
    font-weight: 500 !important;
  }

  .info-table-border table tbody tr td {
    padding: 0.5rem !important;
    font-size: $text !important;
  }
  .asset-allocation-chart,
  .top-account-chart,
  .portfolio-graph {
    width: calc(100% - 1px) !important;
    height: 25rem !important;
  }
  .five-transaction {
    max-height: 15rem;
    overflow: hidden;
    overflow-x: auto;
    .info-table-border {
      overflow: hidden;
      overflow-x: auto;
    }
  }

  .ten-or-more-transaction {
    overflow: auto;
    .info-table-border {
      overflow: auto;
    }
  }

  .big-text-detail {
    font-size: 1.5rem;
    font-weight: 600;
  }
  .big-text-detail-separator {
    padding: 0;
  }
  .big-text-detail-separator {
    div {
      width: 1px;
      height: 100%;
      margin: auto;
    }
  }
  .big-text-detail-value {
    font-size: 1rem;
  }
  .error-popup-view {
    text-align: center;
    .img-fluid {
      width: 46%;
      height: auto;
    }
    .error-500-img{
      width: auto;
      height: 100%;
    }
  }

  .account-error-popup-view {
    text-align: center;
    .img-fluid {
      width: 6.5rem;
      height: auto;
      text-align: right;
    }
    .error-msg {
      padding: 1rem 0px;
      text-align: left;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .line-chart-time-filter {
    a {
      text-decoration: none;
      color: $border;
      font-size: $text;
      font-weight: 500;
    }
    .active {
      color: $orange;
    }
  }

  .sync-loader {
    padding: 0.4rem 0rem;
  }

  .info-icon-adjustment {
    position: relative;
    top: -3px;
  }

  .error-popup-view-chart {
    text-align: center;
    @extend .absolute-center;
    img.image {
      height: auto;
      display: block;
    }
  }
  .accounts-number{
    position: relative;
    top:-3px;
  }
  .filter-icon{
    position: relative;
    top:-2px;
  }
  .filter-container{
    font-size: $text;
    color: $black;
    min-width: calc(20rem);
    
    @extend .box-shadow;
    .search-field{
      margin-bottom: 3px;
    }
    .form-check{
      .checkbox-resize{
        width: 1rem;
        height: 1rem;
        position: relative;
        top: 2px;
        left: 0px;
      }
    }
    .checklist-view{
      max-height: 10rem;
      overflow: auto;
    }
  }
  
  .info-hover-btn{
    margin-left: 5px;
    padding: 0;
  }
  .info-hover-btn.portfolio-snapshot{
    padding:0px;
    svg{
      top:-3px
    }
  }

  .portfilio-90-details-box {
    background-color: $white;
    border-radius: 10px;
    border: 1px solid $border;
    padding: 1rem;
  }

  .portfolio-graph-arrow {
    width: auto;
    height: 15px;
    margin: 7px 0px;
    animation-name: bounceArr;
    animation-duration: 0.5s;
    animation-direction: alternate;
    animation-timing-function: cuzbic-bezier(0.5, 0.05, 1, 0.5);
    animation-iteration-count: infinite;
  }
  @keyframes bounceArr {
    from {
      transform: translate3d(0, 0, 0);
      -webkit-transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(0, -3px, 0);
      -webkit-transform: translate3d(0, -3px, 0);
    }
  }
  
  // Small devices (landscape phones, 576px and up)
@media (min-width: 0) and (max-width: 575px) { 
  .table-size{
    min-width: 20rem !important;
  }
  .portfolio-item{
    text-align: center;
  }
  .section-heading{
    font-size: $normal-text;
    font-weight: 700;
    line-height: 1;
  }
  .checklist-view{
    .form-check{
      .form-check-label{
        margin-top: 7px;
      }
    }
  }
  .add-account-btn{
    font-size: $header - 7px;
  }
  .user-portfolio-heading{
    font-size: $header - 2px;
  }
  .detail-d-none{
    display: none;
  }
  .details-box{
    height: auto;
  }
 }
 
  // Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767px) { 
  .detail-d-none{
    display: inline;
  }
  .add-account-btn{
    font-size: $header - 7px;
  }
  .user-portfolio-heading{
    font-size: $header - 2px;
  }
  .table-size{
    min-width: 20rem !important;
  }
  .portfolio-item{
    text-align: center;
  }
  .details-box{
    height: auto;
  }
 }

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991px) { 
  .details-box{
    height: 18rem;
  }
 }

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199px) { 
  .details-box{
    height: 12rem;
  }
 }

