@import "../shared/_common.scss";
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
.about-us {
    *{
        box-sizing: border-box;
        font-family: 'Mukta', sans-serif;
    }
    
    .line {
        margin-top: 50px;
    }
    .text-orange {
        color: $orange;
    }

    .text-purple {
        color: $purple;
    }
    .main-banner {
        width: 100%;
        margin: 0;
        .text-block{
         display:block;
        }
        .para-heading{
            color: $orange;
            font-size: $medium-heading;
        }
        .banner-img {
            margin-top: 60px;
            text-align: end;
        }

        .banner-img-mobile {
            display: none;
        }

        .banner-text {
            margin-top: 100px;
            text-align: start;

            .banner-text-padding {
                font-size: $big-header;
                margin-bottom: 5px;
                margin-left: 50px;
                font-weight: bold;
            }

            .heading-about-us {
                color: $purple;
                margin-bottom: 22px;
                text-align: center;
            }

            .banner-p {
                margin: 0;
                margin-bottom: 2px;
                font-size: $subheader+1px;
                text-align: center;
            }

            .button-div {
                margin-left: 11rem;
                margin-top: 25px;

                .abt-us-btn {
                    &:hover {
                        background-color: $orange;
                        color: $white;
                    }

                    border: none;
                    background-color: $orange;
                    color:$white;
                    border-radius: 5px;
                    font-weight: 600;
                    padding: 7px 14px;
                    font-size: $big-subheader;
                }
            }
        }

        .banner-text-mobile {
            display: none;
        }

    }

    .crypto-section {
        .content-list-ul {
            .content-list {
                font-size: $big-text;
            }
        }

        .heading-about-us {
            font-size: $big-header;
            font-weight: bold;
            text-align: left;
            margin-top: 15px;
        }

        .crypto-market-heading {
            font-size: $big-text;
            font-weight: bold;
            margin: 15px 0px;
        }

    }

    .content-para {
        font-size: $big-text;
    }

    .content-para-second {
        font-size: $big-text;
        margin-top: -10px;
        .content-para-second-img {
            width: 40px;
            height: 40px;
        }
    }

    .content-para-italic {
        font-style: italic;
        font-size: $big-text;
        color: $black;
    }

    .all-content-section {
        .all-content-head {
            font-size: $big-text;
            font-weight: bold;

        }

        .all-content-para {
            font-size: $big-text;
        }
    }

    .about-us-content {
        margin-bottom: 50px;

        .content-last-section {
            font-weight: bold;
            font-size: $big-text;
        }

        .simplify-content {
            font-size: $big-text;
            font-weight: bold;
            margin-bottom: -5px;

        }

        .last-section-content-ul {
            margin-top: 30px;

            .last-section-content-li {
                font-size: $big-text;

                .text-bold {
                    font-weight: bold;
                    color: $purple;
                    font-size: $subheader;
                }

            }
        }

        .last-section-header {
            font-weight: bold;
        }

        .abt-us-content {
            margin-bottom: 15px;
            font-size: $big-text;
        }

        .sub-headings {
            font-size: $big-header;
            padding-bottom: 5px;
            margin-bottom: 20px;
            margin-top: 24px;
            font-weight: bold;

           
        }
    }
    .sliced {
        border-bottom: 2px solid $orange;
    }

    @media (max-width: 1200px) {
        .last-section-header {
            font-size: $big-header+10px !important;
            color: $purple;
            font-weight: bold;
        }

        .main-banner {
            .right-content {
                padding-left: 5%;

                .banner-text {


                    .banner-text-padding {
                        margin-left: 0px;

                    }

                    .heading-about-us {
                        font-size: $head-mobile;
                        color: $purple;
                        margin-bottom: 14px;
                    }

                    .banner-p {
                        margin-bottom: 1rem;
                        font-size: $big-text;
                    }

                    .button-div {
                        margin-left: 7.5rem;
                    }
                }
            }

        }
    }

    @media (max-width: 991px) {
        .main-banner {
            .right-content {
                padding-left:2rem;

                .banner-text {
                    .banner-text-padding {
                        font-size: $subheader;
                    }

                    .banner-p {
                        margin-bottom: 1rem;
                        font-size: $small-text;
                    }

                    .button-div {
                        margin-left: 4.5rem;
                    }
                }
            }

        }
    }

    @media (max-width: 770px) {
        .last-section-header {
            font-size: $big-header !important;
            color: $purple;
            font-weight: bold;
        }

        .main-banner {
            .banner-img {
                display: none;
            }

            .banner-text {
                display: none;
            }

            .banner-img-mobile {
                display: block;

            }

            .banner-text-mobile {
                display: block;
                margin-top: 40px;

                .text-orange {
                    color: $orange;
                }

                .text-purple {
                    color: $purple;
                }

                .banner-text-padding {
                    font-size: $header;
                    margin-bottom: 5px;
                    font-weight: bold;
                }

                .banner-p {
                    font-size: $big-text;
                    color: #2c2c2c;
                }

                .abt-us-btn {
                    margin-top: 30px;

                    &:hover {
                        background-color: $orange;
                        color: $white;
                    }

                    border: none;
                    background-color: $purple;
                    color:$white;
                    border-radius: 50px;
                    width: 143px;
                    height: 39px;
                    font-size: $big-text;
                }
            }
        }

    }

    @media (max-width: 500px) {
        .banner-text-padding {
            font-size: $big-text+1px !important;
            margin-bottom: 5px;
            font-weight: bold;
        }
        .para-heading{
            font-size: $big-header !important;
        }
    }

}