@import '../../shared/_common.scss';

.investment-strategies {
  @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
    *{
        box-sizing: border-box;
        font-family: 'Mukta', sans-serif;
    }
  .bg-image {
    background-image: url($env+"/investment-strategy-background.svg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
  }
  .text-bold{
    font-weight: bold;
  }
  .text {
  
    .header {
      font-size: $big-header;
      font-weight: bold;
      color: $purple;
    }
.main-sub-header{
  font-size: 30px;
  color: $orange;
  font-weight: bold;
}
    .main-header {
      font-size: $head;
      font-weight: bold;
      color: $purple;

      span {
        color: $orange;
    

      }
    }

    .para {
      font-size: $big-text;
    }
  }

  .col-text {
    .header-col {
      font-size: $big-text;
      font-weight: 700;
      color: #2c2c2c;
    }

    .para-col {
      font-size: $big-text;
      text-align: justify;
      width: 13rem;
      margin: auto;
    }
  }

  .image-div {
    .square-images {
      margin: 0rem 1rem;
    }
  }

  .launch-banner-div {
    position: relative;
    min-height: 56rem;
    width: 100%;
  }

  .launching-soon-div {
    height: 55.688rem;
    width: 100%;
    background-color: $white;
    animation: showAnimation 3s forwards ease;
    z-index: +2;
  }

  .animation-start {
    height: 100%;
    position: absolute;
    width: 100%;
    animation: hideAnimation 1s forwards;
  }


  .content-black-div {
    padding-top: 30px;
  }


  @media (max-width: 768px) {
    .text {
      .main-header {
        font-size: $big-header;
      }
    }
    .main-sub-header{
      font-size:22px !important;
    }

    .images-mobile {
      .image-div {

        border: 2px solid $orange;
        border-radius: 28px;

      }
    }

    .animation-start {
      height: 100%;
    }

    .text {
      .header {
        font-size: 20px;
        font-weight: bold;
        color: $purple;
      }

      .para {
        font-size: $normal-text;
      }
    }
  }

}