@import "../shared/_common.scss";

$border-width: 1px solid $border;
$green-border: 1px solid $dark-green;
$background-grad: linear-gradient(#FFFFFF, #F4F8FF) !important;
$border-error: 1px solid #A2251B;

@mixin new-progress-bar($progress: calc(100% - 10.5rem)) {
    width: $progress;
    height: 2rem !important;
    margin-left: 0.4rem;
}

@mixin error2($backcolor: #FFF0F2, $bordercolor: 1px solid #FFDBE0, $margin: 10px) {
    font-size: $normal-text;
    font-weight: 400;
    border-radius: 13px;
    height: auto;
    width: 99%;
    padding-bottom: 0px !important;
    background-color: $backcolor;
    border: $bordercolor  !important;
    margin-left: $margin;
}

@mixin cross-prog($marginright: -10px) {
    top: 0px;
    left: 100%;
    float: right;
    margin-right: $marginright  !important;
    text-decoration: none;
    margin-top: -10px;
    cursor: pointer;
}

@mixin polygon-2-prog($padding: 35px) {
    padding-left: $padding;
    padding-bottom: 0px;
    padding-top: 0px;
    margin-top: -5px;
}

@mixin incomplete-para($textcolor: #B5747E, $colorspan: #8C0858, $paddingleft: 10px) {
    color: $textcolor;
    line-height: normal;
    padding-left: $paddingleft;
    padding-top: 10px;

    span {
        color: $colorspan;
        font-weight: bold;
    }
}

.add-account {
    .new-progress-bar {
        #incom-prog {
            background-color: #DDABB1;
        }
    }

    .main-container {
        margin-top: 6rem !important;
        border: $border-width;
        border-radius: 13px;
        opacity: 0.9;

        .go-back {
            margin-top: 15px;
        }
    }

    .first-part {
        border-right: 2px solid $purple;
        padding-left: 32px !important;

        .error {
            font-size: 17px;
            font-weight: 500;
            padding-bottom: 0px !important;

            .polygon-2-prog {
                @include polygon-2-prog;
            }

            .error2 {
                @include error2($margin: 0px);
                margin-top: -7px;

                .warning-para {
                    @include incomplete-para;
                }
            }
        }

        .new-account {
            position: relative;
            margin-bottom: 30px;

            .main-progress {
                width: 99%;
                height: 52px;
                border: 1px solid #74A669;
                border-radius: 12px;
                display: flex;
                justify-content: space-between;

                .main-progress-account {
                    font-size: $big-text;
                    font-weight: 600;
                    margin-top: auto;
                    padding-left: 1.2rem;
                    margin: 0;
                    line-height: 2.5;
                    // margin: auto;
                }

                .main-progress-sync {
                    font-size: $big-text;
                    font-weight: 600;
                    color: #6660AB;
                    padding-right: 1.2rem;
                    margin: 0;
                    line-height: 2.5;

                    .syncing-span {
                        padding-left: 5px;
                    }
                }

                .sync-icon {
                    margin: 0;
                    line-height: 2.5;
                    padding-right: 1.2rem;

                    .sync-error {
                        height: 24px;
                        width: 24px;
                    }
                }
            }

            .new-progress-bar {
                @include new-progress-bar ($progress: calc(100% - 2.5rem));

                #add-progress {
                    background-color: $dark-green;
                }
            }

            .progress-bar-text {
                position: absolute;
                top: 2.7rem;
                left: 35%;
            }

            .loader-prog {
                text-align: center;
            }

            .error {
                font-size: 17px;
                font-weight: 500;
                padding-bottom: 0px !important;

                .polygon-2-prog {
                    @include polygon-2-prog;
                }

                .error2 {
                    @include error2($margin: 0px);
                    margin-top: -7px;

                    .cross-prog {
                        @include cross-prog;
                    }

                    .warning-para {
                        .war-continue {
                            cursor: pointer !important;
                            color: $purple  !important;
                            text-decoration: underline !important;
                        }
                    }

                    .warning-para {
                        @include incomplete-para;
                    }
                }
            }

            .error-warning {
                font-size: 17px;
                font-weight: 500;
                padding-bottom: 0px !important;
                margin-top: -6px;
                width: 99%;

                .polygon-warning {
                    padding-left: 35px;
                    padding-bottom: 0px;
                    padding-top: 0px;
                    margin-top: -4px;
                }

                .error2 {
                    @include error2 ($backcolor: #F5FFF4, $bordercolor: 1px solid #74A669, $margin: 0px);
                    margin-top: -7px;

                    .cross-prog {
                        @include cross-prog($marginright: -10px);
                    }

                    .warning-para {
                        .war-continue {
                            cursor: pointer !important;
                            color: $purple  !important;
                            text-decoration: underline !important;
                        }
                    }

                    .warning-para {
                        @include incomplete-para($textcolor: #74A669, $colorspan: #74A669, $paddingleft: 18px);
                    }
                }
            }
        }

        .incomp-exch-div {
            margin-bottom: 10px;
            margin-top: 10px;

            .scroll-div {
                .inprog-main {
                    display: flex;
                    margin-top: 10px;

                    .incom-img {
                        height: 30px;
                        width: 30px;
                    }

                    .new-progress-bar {
                        @include new-progress-bar;

                        .incomplete-progress {
                            background-color: #cc8d95;
                        }
                    }

                    .incomplete-retry-div {
                        margin-top: 5px !important;
                        margin-left: 20px;
                        margin-right: -40px;
                        display: flex;

                        .reconnect-logo {
                            cursor: pointer;
                            height: 22px;
                            width: 30px;
                        }

                        .retry-prog {
                            font-size: $normal-text;
                            font-weight: 600;
                            color: $purple;
                            text-decoration: underline;
                            margin-left: 4px;
                            cursor: pointer;
                        }

                        .prog {
                            width: 30% !important;
                        }
                    }

                    .incomplete-delete {
                        margin-left: 50px;
                        width: 18px;
                        height: 18px;
                        margin-top: 0.5rem;
                        cursor: pointer;
                    }
                }

                .inprog-warning {
                    margin-bottom: 15px;

                    .polygon-2-prog {
                        @include polygon-2-prog;
                    }

                    .cross-prog {
                        @include cross-prog;
                    }

                    .incomplete-error {
                        font-size: 17px;
                        font-weight: 400;
                        border-radius: 13px;
                        width: 95%;
                        height: auto;
                        padding-bottom: 0px !important;
                        background-color: #FFF0F2;
                        border: 1px solid #FFDBE0 !important;
                        margin-left: 10px;
                        margin-top: -7px;

                        .cross-prog {
                            @include cross-prog
                        }

                        .incomplete-para {
                            @include incomplete-para
                        }
                    }
                }
            }

            .progress {
                display: flex;
                height: 2rem !important;
                overflow: hidden;
                font-size: $sub-text;
                background-color: #e9ecef;
                border-radius: 0.25rem;
            }
        }

        .first-header {
            color: $purple;
            font-size: $header;
            margin-bottom: 0.7rem;
            font-weight: bold;
        }

        .first-para {
            font-size: $normal-text;
            line-height: normal;
        }

        .first-input {
            width: 98.5%;
            padding-top: 9px;
            padding-bottom: 9px;
            border-radius: 12px;
            border: $border-width;
        }

        .boxes {
            margin-top: 15px;
            border-radius: 13px;
            width: 99%;
            margin-left: 0px;
            margin-bottom: 10px;
            height: 410px;
            overflow-y: scroll;
            overflow-x: hidden;
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            border: 1px solid #BABABA !important;

            .loader-accounts {
                margin: auto;
                text-align: center;
            }

            .add-button {
                height: 82px;
                width: 117px;
                font-size: $normal-text;
                border-radius: 13px;
                margin-top: 13px !important;
                background: $background-grad;
                border: $border-width;
                margin-left: 5px;
            }

            .clicklable-section-carousel {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                cursor: wait;
            }

            .boxes-account {
                height: 30px;
                width: 30px;
            }
        }
    }

    .prog {
        width: 30%;
    }

    .second-part {
        margin-top: 0px !important;
        padding: 0rem 2rem;

        .second-header {
            font-size: $header;
            font-weight: bold;
            color: $purple;
        }

        .sync-all {
            background-color: $purple;
            color: white;
            border: none;
        }

        .not-expired {
            font-size: $normal-text;
            line-height: 1;
        }

        .not-expired {
            margin-top: 0px !important;
        }

        .expired-account {
            border: $border-width;
            border-radius: 14px;
            text-align: center;
            height: auto;
            margin-bottom: 50px;
            margin-top: 10px !important;
            background: $background-grad;

            .expired-img {
                margin-top: 1rem;
            }

            .expired-para {
                font-size: $normal-text;
                margin-top: 10px;

                span {
                    font-weight: bold;
                }
            }
        }

        .connected-account {
            border: $border-width;
            border-radius: 14px;
            text-align: center;
            height: auto;
            margin-top: 25px !important;
            background: $background-grad;

            .connected-img {
                margin-top: 1rem;
            }

            .connected-para {
                font-size: $normal-text;
                margin-top: 10px;
            }

            span {
                font-weight: bold;
            }
        }

        .scroll-div {
            overflow-y: scroll;
            height: auto;
            max-height: 300px;
            padding: 0px !important;
            margin-bottom: 25px;
            padding: 0px 10px 0px 0px !important;
            overflow-x: hidden;

            .loader-account {
                border: 1px solid $border;
                border-radius: 13px;
                height: 285px;
                text-align: center;
                margin-top: 10px;
                background: $background-grad;

                .loader-image {
                    margin: auto;
                    margin-top: 7rem;
                }
            }

            .boxes-2 {
                border: $border-error;
                border-radius: 13px;
                background: $background-grad;
                margin-top: 0px !important;
                padding-right: 0px !important;
                margin-right: 0px !important;
                margin-bottom: 20px;

                .inside-box {
                    border-bottom: $border-error;
                    border-top-left-radius: 13px;
                    border-top-right-radius: 13px;
                    width: 100%;
                    height: 38px;

                    .inside-box-img {
                        margin-right: 3px;
                    }

                    .inside-box-para {
                        margin-left: 42%;
                        font-size: $subheader  !important;
                        align-items: center;
                    }
                }
            }

            .boxes-2-expired {
                margin-top: 20px !important;
                margin-bottom: 0px !important;

                .boxes-2-con-ins {
                    background-color: #F5FFF4 !important;

                    .inside-box-para {
                        font-size: $subheader  !important;
                        font-weight: 600;
                    }
                }

                .inside-box-3 {
                    .inside-box-delete {
                        margin-right: 12px;
                        margin-left: 10px;

                        .syncing-connected {
                            margin-left: 10px;
                            display: none;
                            padding-top: 7px;
                        }

                        button {
                            border: none;
                            background: transparent;
                        }

                        .add-opacity {
                            opacity: 0.5;
                        }

                        .sync {
                            font-size: $big-text  !important;
                            color: $purple;
                            cursor: pointer;
                            font-weight: 600;
                            margin-left: 5px;
                            text-decoration: underline;
                        }
                    }
                }
            }

            .boxes-2-connect {
                margin-top: 20px !important;
                border: $green-border  !important;
                margin-bottom: 0px !important;

                .boxes-2-con-ins {
                    border-bottom: $green-border  !important;
                    background-color: #F5FFF4 !important;

                    .inside-box-para {
                        font-size: $subheader  !important;
                        font-weight: 600;
                    }
                }

                .inside-box-3 {
                    .disable-delete {
                        position: absolute;
                        right: 0;
                        width: 7rem;
                        top: 0;
                        bottom: 0;
                    }

                    .inside-box-delete {
                        margin-right: 12px;
                        margin-left: 10px;

                        .syncing-connected {
                            margin-left: 10px;
                            display: none;
                            padding-top: 7px;
                        }

                        button {
                            border: none;
                            background: transparent;
                        }

                        .add-opacity {
                            opacity: 0.5;
                        }

                        .sync {
                            font-size: $big-text  !important;
                            color: $purple;
                            cursor: pointer;
                            font-weight: 600;
                            margin-left: 5px;
                            text-decoration: underline;
                        }
                    }
                }
            }

            .error-connected {
                margin-top: -5px;

                .error-connect-img {
                    padding-left: 35px;
                    padding-bottom: 0px;
                    padding-top: 0px;
                    margin-top: 5px;
                }

                .error2 {
                    @include error2;

                    .error2-para {
                        color: #B5747E;
                        line-height: normal;
                        padding-top: 10px;

                        span {
                            color: #8C0858;
                            font-weight: bold;
                        }
                    }
                }

                .error-2-warning {
                    align-items: center;
                    margin-left: 0px;
                    height: 65px;

                    .cross-connected {
                        top: 0px;
                        left: 100%;
                        float: right;
                        margin-right: -27px !important;
                        text-decoration: none;
                        margin-top: -12px;
                        cursor: pointer;
                    }

                    .error-2-img {
                        cursor: pointer;
                        color: $purple;
                        text-decoration: underline;
                    }
                }
            }

            .sync-warning {
                .error {
                    font-size: 17px;
                    font-weight: 500;
                    padding-bottom: 0px !important;

                    .polygon-2-prog {
                        @include polygon-2-prog;
                    }

                    .error2 {
                        @include error2($margin: 0px);
                        margin-top: -7px;

                        .cross-prog {
                            @include cross-prog;
                        }

                        .warning-para {
                            .war-continue {
                                cursor: pointer !important;
                                color: $purple  !important;
                                text-decoration: underline !important;
                            }
                        }

                        .warning-para {
                            @include incomplete-para;
                        }
                    }
                }
            }

            .success {
                font-size: 17px;
                font-weight: 500;
                padding-bottom: 0px !important;

                .polygon-2-prog {
                    padding-left: 35px;
                    padding-bottom: 0px;
                    padding-top: 0px;
                    margin-top: -10px;
                }

                .success-2 {
                    font-size: $normal-text;
                    font-weight: 400;
                    border-radius: 13px;
                    height: auto;
                    padding-bottom: 0px !important;
                    background-color: #F5FFF4;
                    border: 1px solid $dark-green  !important;
                    margin-left: 10px;
                    margin-top: -10px;

                    .cross-connected {
                        top: 0px;
                        left: 100%;
                        float: right;
                        margin-right: -23px !important;
                        text-decoration: none;
                        margin-top: -14px;
                        cursor: pointer;
                    }

                    .success-para {
                        color: $dark-green;
                        line-height: normal;
                        padding-left: 10px;
                        padding-top: 10px;

                        span {
                            color: $dark-green;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
    }

    .desktop-form {
        display: block;
    }

    .mobile-form {
        display: none;
    }

    .form-para {
        .info-icon {
            padding-bottom: 2px;
        }

        .click-here {
            padding-left: 4px;
            color: $purple;
            text-decoration: underline;
            cursor: pointer;
        }
    }

    .details-form {
        border: 1px solid #BABABA;
    }

    .bot-form {
        .broken-robot {
            height: 268px;
            width: 242px;
        }
    }
}

@media (min-width:1200px) and (max-width:1333px) {
    .first-part {
        .add-button {
            width: 93% !important;
            font-size: $text  !important;
        }
    }
}

@media (min-width:767px) and (max-width:864px) {
    .first-part {
        .add-button {
            width: 95% !important;
            font-size: $text  !important;
        }
    }
}

@media screen and (min-width:767px) and (max-width:823px) {
    .add-account {
        .first-part {
            .boxes {
                .add-button {
                    width: 100%;
                    font-size: $text;
                }
            }
        }

        .second-part {
            padding: 0rem 1rem 0rem 1.8rem;

            .second-header {
                font-size: $header;
            }

            .sync-all {
                margin-bottom: 2px !important;
                padding: 3px 9px !important;
            }

            .scroll-div {
                .boxes-2 {
                    .inside-box {
                        .inside-box-para {
                            margin-left: 35%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:767px) {
    .add-account {
        .mobile-form {
            display: block;
        }

        .desktop-form {
            display: none;
        }

        .form-div {
            .form-heading {
                font-size: $subheader;
            }
        }
    }
}

@media screen and (max-width:488px) {
    .add-account {
        .second-part {
            padding: 0rem 1rem 0rem 1.8rem;

            .second-header {
                font-size: $header;
            }

            .sync-all {
                margin-bottom: 2px !important;
                padding: 3px 9px !important;
            }

            .scroll-div {
                .boxes-2 {
                    .inside-box {
                        .inside-box-para {
                            margin-left: 37%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:390px) {
    .add-account {
        .first-part {
            .boxes {
                .add-button {
                    width: 100%;
                    font-size: $text;
                }
            }

            .form-div {
                .container {
                    padding-left: 0px !important;
                    padding-right: 0px !important;
                }
            }
        }
    }
}