@import "../../shared/common";

.user-preferences {
    .background-map {
        background: url($map);
        background-position: center;
        background-size: cover;
        padding-top: 2rem;
        padding-bottom: 2rem;

        .form-div-container {
            background: $white;
            border-radius: 10px;
            box-shadow: $box-shadow;
            width: 650px;
            margin: auto;
            padding: 0px;

        }

        .top-banner {
            background: #f7f7f7;
            width: 100%;
            padding: 20px 0px;
            border-radius: 10px 10px 0px 0px;
            position: relative;

            .cross-btn {
                position: absolute;
                right: -10px;
                top: -10px;
                cursor: pointer;
            }
        }
    }

    .settings-h1 {
        font-size: $head-mobile;
        color: $purple;
        font-weight: 500;
    }

    .hello-user {
        margin-bottom: 0px;
        max-width: 380px;
        margin: auto;
        border: 1px solid $grey;
        font-weight: 500;
        color: #273251;
        font-size: $header;
        background: #f3f3f3;
        overflow: hidden;
        border-radius: 10px;
    }

    .active-btn {
        width: 100%;
        border: none;
        color: $white;
        padding: 8px 0px;
        font-size: $big-text;
        font-weight: 500;
        border-left: 4px solid $orange;
        text-align: left;
        margin-bottom: 1px;
        background: $purple;
        cursor: default;
        padding-left: 5px;
    }

    .left-btns {
        width: 100%;
        border: none;
        color: #333333;
        padding: 8px 0px;
        font-size: $big-text;
        border-left: 4px solid #cfcddc;
        margin-bottom: 1px;
        text-align: left;
        padding-left: 5px;

        &:hover {
            text-decoration: underline;
        }
    }

    .user-info-h1 {
        font-size: $semi-big-header;
        color: $purple;
        font-weight: 500;

        @media(max-width:767.5px) {
            font-size: $subheader;
        }
    }

    .icon-width-fixing {
        width: 30px;
        height: auto;
        text-align: center;
        display: inline-block;
    }

    .drop-menu {
        border-radius: 10px;
        font-size: $normal-text;
    }

    .sub-head {
        font-size: $big-text;
        color: $purple;

        @media(max-width:767.5px) {
            font-size: $normal-text;
        }
    }

    .button-submit {
        background-color: $green;
        margin-top: 15px;
        margin-right: 10px;
        font-size: $big-text;
        border: 1px solid $dark-green;
        border-radius: 10px;
        padding: 5px 10px;
        text-decoration: none;
        text-decoration: none;

        @media(max-width:767.5px) {
            font-size: $normal-text;
        }

        &:hover {
            color: $white;
            background-color: $orange;

            &:disabled {
                color: #1010104D;
                background-color: $green ;
            }
        }
    }

    .toggle-border {
        border-top: 1px solid $middle-grey  !important;
        padding-top: 1rem;
        margin-bottom: 1rem;
    }

    .form-check-input {
        width: 43px !important;
        height: 22px !important;
        margin-right: 1rem;

        &:checked {
            background-color: $dark-green;
            border: 1px solid $dark-green;
        }
    }

    .toggle-label {
        color: $purple;
        font-size: $normal-text;
    }

    .success {
        background: $success-msg-green;
        position: relative;
        color: $success-msg-green-border;
        width: 100%;
        border-radius: 10px;
        border: 1px solid $success-msg-green-border;
        padding: 0.3rem;

        .success-cross {
            position: absolute;
            top: -5px;
            right: -5px;
            cursor: pointer;
        }

        .success-para {
            word-wrap: break-word;
            margin: 0px;
        }
    }

    .error2 {
        background: $error-msg-red;
        color: $error-msg-red-text;
        width: 100%;
        border-radius: 10px;
        border: 1px solid $error-msg-red-border;
        position: relative;
        padding: 7px 14px;

        .error-para {
            word-wrap: break-word;
        }

        .error-cross {
            position: absolute;
            top: -6px;
            right: -6px;
            cursor: pointer;
        }
    }

    .info-hover-btn {
        margin-left: 5px;
        padding: 0;
    }

    .user-portfolio-info-icon {
        width: 1rem;
        height: 1rem;
        position: relative;
        top: 0px;
        fill: $tooltip-icon;

        svg {
            fill: $tooltip-icon;
            position: relative;
            top: 0px;
        }
    }

    @media(max-width:767.5px) {
        .user-preferences-container {
            padding-right: 2rem;
            padding-left: 2rem;
            padding-top: 1rem;
        }

        .settings-h1 {
            margin-top: 5rem;
            font-size: $semi-big-header;
            color: $purple;
            font-weight: 500;

            .settings-icon {
                width: 25px;
            }
        }

        .hello-user {
            margin-bottom: 0px;
            max-width: 380px;
            margin: auto;
            border: 1px solid $grey;
            font-weight: 500;
            color: #273251;
            font-size: $semi-big-header;
            background: #f3f3f3;
            overflow: hidden;
            border-radius: 10px;

            @media(max-width:390px) {
                margin-left: 10px;
                margin-right: 10px;
            }
        }

        .active-btn {
            width: 100%;
            border: none;
            color: $white;
            padding: 8px 0px;
            font-size: $big-text;
            font-weight: 500;
            text-align: left;
            background: $purple;
            cursor: default;
            padding-left: 5px;
        }

        .left-btns {
            width: 100%;
            border: none;
            color: #333333;
            padding: 8px 0px;
            font-size: $big-text;
            border-bottom: 1px solid #939393;
            text-align: left;
            padding-left: 5px;

            &:hover {
                text-decoration: underline;
            }
        }

        .btn-arrow-1 {
            margin-left: calc(100% - 10.8rem);
        }

        .btn-arrow-2 {
            margin-left: calc(100% - 12rem);
        }

        .btn-arrow-3 {
            margin-left: calc(100% - 13.3rem);
        }

        .btn-arrow-4 {
            margin-left: calc(100% - 10.6rem);
        }
    }
}