.reset-pwd-page {
    @import '../../../sass/shared/variable.scss';

    .confirm-pwd-info {
        background-color: $signup-info-box;
        border: 1px solid $signup-info-box-border;
        border-radius: 10px;
        font-size: 17px;
        font-weight: 600;
        padding-left: 20px;
    }

    .pwd-info {
        background-color: $signup-info-box;
        border: 1px solid $signup-info-box-border;
        border-radius: 10px;
        padding: 1rem;
        margin-bottom: 10px;

        .text-green {
            color: $success;
        }

        .text-red {
            color: $failure;
        }

        .pwd-info-heading {
            font-size: 17px;
            font-weight: 600;
        }

        .the-info {
            margin: 0;
            padding: 0;
            list-style: none;

            .icon {
                position: relative;
                top: -2px;
            }
        }

    }

    .navbar-brand img {
        width: 300px;
        height: 50px;
    }

    .form-container {
        background: $white;
        padding: 30px;
        border-radius: 10px;
        margin-top: 15px;

        .eye-icon-container {
            position: relative;

        }

        .eye-icon {
            position: absolute;
            color: $grey  !important;
            right: 10px !important;
            top: 40px;

        }




        input {
            margin: 3px 0px;
            border-radius: 5px;
            font-size: $big-text;
        }
        .text-heading{
            color: $purple;
            font-weight: bold;
        }
        .submit-form {
            font-size: $big-text;
            border-radius: 5px;
            font-weight: 600;
            cursor: pointer;
            border: none;
            outline: none;
            background-color: $orange;
            color: $white;
            margin-bottom: 20px;
            margin-top: 20px;

        }

        span {
            color: $danger;
        }

        .form-group {
            margin-bottom: 10px;
        }

        label {
            margin-bottom: 5px;
            color: $black;
            font-size: $big-text;
        }

    }

    .form-container-head {
        font-size: $semi-big-header+1px;
        margin-bottom: 10px;
        color: $purple;
    }

    .form-container-para {
        font-size: $big-text;
    }



    .btn-submit {
        font-size: $big-text;
        border-radius: 5px;
        margin-top: 20px;
        background-color: $orange;
        color: $white;
        font-weight: 600;
        border: none;
    }

    .error-container {
        background: $white;
        padding: 30px;
        max-width: 500px;
        margin: auto;
        border-radius: 10px;
        margin-top: 15px;

        .submit-form {
            font-size: $big-text;
            border-radius: 5px;
            padding: 0.5rem 0rem;
            font-weight: 600;
            cursor: pointer;
            border: none;
            outline: none;
            background-color: $orange;
            color: $white;
            margin-bottom: 20px;
            margin-top: 20px;

        }

        .error-para {
            font-size: 24px;
        }

    }
}