@import '../../shared/common';

.all-exchange{
    .banner-heading{
        font-size: $big-header;
        line-height: 39px;
    }
    .text-block{
        display: block;
        color: $purple !important;
    }
    .banner-sub-heading{
        font-weight: bolder;
        font-size: 34px;
        color: $orange !important;
    }
    .text-container{
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }
    .top-div-border{
        border-bottom: 1px solid $black;
    }
    
    .heading{
        color: $purple;
        font-size: $big-header;
        text-align:center;
        font-weight: 500;
        @media(max-width:991.5px){
            font-size: $big-subheader;
            .banner-heading{
                font-size: $big-subheader;
            }
        }
        span{
            color:$orange;
        }
    }
    
    .subheading{
        font-size: $big-text;
        text-align: center;
        @media(max-width:991.5px){
            font-size: $big-text;
        }
        span{
            font-size: $big-text;
            font-weight: bold;
            @media(max-width:991.5px){
                font-size: $normal-text;
            }
        }
    }

    .btn-getstarted{
        background-color: $orange;
        color: $white;
        font-size: $big-subheader;
        border-radius: 5px;
        font-weight: 600;
        margin-top: 3.188rem;
        padding-left: 20px;
        padding-right: 20px;
        @media(max-width:767.5px){
            margin-top: 0.118rem;
            margin-bottom: 1rem;
        }
        &:hover{
            background-color: $orange;
            color: $white;
        }
    }
}