@import '../shared/_common';

.launch-soon-plz {
  @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
  *{
      box-sizing: border-box;
      font-family: 'Mukta', sans-serif;
  }

  .launching-soon-banner {
    .launch-soon-button {
      background-color: $orange;
      border: none;
      padding: 0.5rem 1rem;
      color: $white;
      text-decoration: none;
      font-size: 20px;
      font-weight: 600;
      border-radius: 5px;
      cursor: pointer;
    }

    .sub-text {
      margin-top: 31px;

      .sub-text-content {
        font-size: 18px;
        margin-bottom: 0;
      }
    }

    .get-Notified-Link {
      text-decoration: underline;
      color: $purple;
      border: none;
      background: none;
      cursor: pointer;
    }

    // RESPONSIVE DISPLAY

    @media (max-width: 770px) {
      .sub-text {
        padding: 0;
        margin-left: 30px;
        margin-right: 30px;
      }

    }
  }
}