@import "./common";

.twoFA {
    .form-box {
        max-width: 570px;
        min-width: 340px;
        background: $white;
        padding: 10px 30px 30px 30px;
        border-radius: 5px;
        box-shadow: $light-box-shadow;
    }

    .logo {
        margin: 10px 0px;
    }

    .crptm-brand {
        width: 300px;
        height: 50px;
    }

    .header {
        font-size: $big-text;

        span {
            font-weight: 600;
        }
    }

    .sub-header {
        font-size: $big-text;
        color: $middle-grey;
    }

    .cancel-btn {
        font-size: $big-subheader;
        border-radius: 5px;
        background: $tooltip-bg;
        border: none;
        padding: 9px 30px;
    }

    .verify-btn {
        @extend .cancel-btn;
        color: $white;
        background: $orange;

        &:disabled {
            opacity: 70%;
        }
    }

    .otp-label {
        font-size: $subheader;

        span {
            color: $danger;
        }
    }

    .otp-input {
        @extend .otp-label;
        border-radius: 5px;
        border: 1px solid $middle-grey;
        padding: 0.5rem;
        padding-right: 8.5rem;
    }

    .otp-div {
        position: relative;

        .timer {
            padding-right: 2rem;
            position: absolute;
            top: 0.75rem;
            right: 1rem;
            border-left: 1px solid #939393;
            padding-left: 3rem;
        }

        .re-send {
            position: absolute;
            right: 1rem;
            top: 0.75rem;
            border-left: 1px solid $middle-grey;
            padding-left: 1rem;

            button {
                color: $purple;
                border: none;
                background: transparent;

                &:hover {
                    text-decoration: underline;
                    cursor: pointer;
                }

                &:disabled {
                    text-decoration: none;
                    cursor: default;
                    opacity: 70%;
                }
            }
        }
    }

    .error2 {
        background: $error-msg-red;
        color: $error-msg-red-text;
        width: 100%;
        border-radius: 5px;
        border: 1px solid $error-msg-red-border;
        position: relative;
        padding: 7px 14px;
        margin-top: 13px;

        .error-triangle {
            position: absolute;
            top: -13px;
            left: 10px;
        }

        .error-para {
            font-size: $big-text;
            word-wrap: break-word;

            span {
                color: $berry;
                font-weight: bold;
            }
        }

        .error-cross {
            position: absolute;
            top: -6px;
            right: -6px;
            cursor: pointer;
        }
    }

    @media(max-width:767.5px) {
        .crptm-brand {
            width: 228px;
            height: 32px;
        }

        .header {
            font-size: $normal-text;
        }

        .sub-header {
            font-size: $normal-text;
        }

        .cancel-btn {
            font-size: $normal-text;
            border-radius: 5px;
            background: $tooltip-bg;
            border: none;
            padding: 4.5px 15px;
        }

        .verify-btn {
            @extend .cancel-btn;
            color: $white;
            background: $orange;

            &:disabled {
                opacity: 70%;
            }
        }

        .otp-label {
            font-size: $normal-text;
        }

        .error2 {
            .error-para {
                font-size: $normal-text;
            }
        }
    }
}