@import '../../shared/common';

.form-div {
    background: linear-gradient(to bottom, $white, $white);
    padding: 18px 0px;

    .bot-image {
        margin: 14px 0px;
    }

    .img-mascot {
        width: 270px;
        height: 212px;
    }

    .bot-para {
        font-size: 2rem;
        position: relative;

        @media(max-width:991.5px) {
            font-size: $subheader+1px;
        }
    }

    .alignment-center {
        position: absolute;
        transform: translate(25%, 50%);
    }

    .btn-clickhere {
        background-color: $orange;
        color: $white;
        font-size: $big-subheader;
        font-weight: 500;
        border-radius: 5px;
        padding: 5px 20px;

        @media(max-width:991.5px) {
            font-size: $big-text+1px;
        }

        &:hover {
            background-color: $orange;
            color: $white;
        }
    }

    .bot-form {
        border-radius: 10px;
        border: 1px solid $border !important;
        padding-top: 16px;
        padding-left: 2rem;
        padding-right: 2rem;
        background-color: $white;

        @media(max-width:767.5px) {
            padding-left: 0px;
            padding-right: 0px;
        }

        .broken-robot {
            height: 268px;
            width: 242px;
        }
    }

    .form-heading {
        color: $purple;
        font-size: $header+1px;
        font-weight: bold;
        margin-bottom: 28px;

        span {
            color: $orange;
        }
    }

    .form-txt {
        font-size: $big-text;

        span {
            color: $danger;
        }
    }

    .form-cross {
        cursor: pointer;
    }

    .form-input {
        height: 2.5rem;
        border: 1px solid $border;
        border-radius: 10px;
        padding-left: 8px;

        &::placeholder {
            color: $border;
        }
    }

    .add-more-tag {
        color: $purple;
        cursor: pointer;
    }

    .submit-btn {
        background-color:$home-btn;
        font-size: $big-text+1px;
        border-radius: 5px;
        padding: 7px 24px;
        border: none;
        color: $white;

        &:disabled {
            color: $banner-para;
            background-color: $tooltip-bg;

            &:hover {
                color: $banner-para;
                background-color: $tooltip-bg;
            }
        }

        &:hover {
            background-color: $orange;
            color: $white;
        }
    }

    .captcha-row {
        margin-top: 15px;
        margin-bottom: 34px;
    }

    .success-div {
        border: 2px dashed $orange;
        border-radius: 10px;
        padding: 0rem 3rem;
        max-width: 34rem;
        margin: auto;

        .success-heading {
            color: $purple;
            font-size: $semi-big-header+2px;
            font-weight: bold;
        }

        .success-para {
            font-size: $big-text;
        }
    }

    .error-div {
        position: relative;

        .error-para {
            font-size: $head-mobile;
        }

        .retry-btn {
            background: $purple;
            color: $white;
            border: none;
            border-radius: 25px;
            padding: 0.5rem 1.5rem;

            &:hover {
                background: $orange;
            }
        }

        .error-text-div {
            width: 100%;

            @media(min-width:768px) {
                position: absolute;
                top: 6rem;
                right: 2rem;
            }
        }
    }
}