@import "../shared/_variable.scss";
@import "../shared/_common.scss";


$border-width: 1px solid $light-grey;

.import {
    .text-purple {
        color: $purple;
        text-decoration: underline !important;
        font-size: $header;
        font-weight: 600;
    }

    .main-contain {
        border-radius: 13px;
        box-shadow: 0em 0em 0.06em 0.06em $border;
        margin-top: 6rem !important;
        background-image: $map;

        .error-key {
            border-radius: 13px !important;

            .error-para {
                font-size: $big-text;
                margin-bottom: auto;
                color: $dark-red;
                font-weight: 500;

                span {
                    margin-top: 5px;
                }
            }

            .error-anchor {
                cursor: pointer;

                .error-crypto {
                    height: 25px;
                    width: 25px;
                }
            }
        }

        .body-change {
            width: calc(100% - 2rem);
            border-bottom: 1px solid $grey;
            margin: auto;
            margin-top: 1.9rem;

            .body-header {
                color: $purple;
                font-size: $header;
                font-weight: 600;
            }

            .body-change-2 {
                button {
                    font-size: $header;
                    font-weight: bold;
                    letter-spacing: 0px;
                    padding-bottom: 0px !important;
                    padding-top: 3px;
                    border: none;
                    width: auto;
                    background: none;
                    padding-left: 20px;
                    margin-top: -5px;
                }
            }
        }

        .content-section {
            margin-top: 50px !important;

            .content-section-header {
                color: $purple;
                font-size: $header;
                margin-top: 1rem;
            }

            .content-section-para {
                font-size: $big-text;

                span {
                    font-weight: 600;
                }
            }
        }

        .import-trades {
            margin-left: 25% !important;

            button {
                width: 68% !important;
                height: 40px;
                border-radius: 5px;
                background-color: $orange;
                font-size: $big-text;
                color: $white;
                text-align: center;
                margin-bottom: 70px;
                &:hover {
                    color: $white !important;
                    background-color: $orange !important;
                }

                &:active {
                    color: $white !important;
                    background-color: $orange !important;
                }

            }

            .loader-oauth {
                margin-bottom: 50px;
                margin-left: 12rem;
            }
        }

        .content-api {
            margin: auto;
            margin-top: 35px;
            padding-left: 18px;
            padding-right: 0px !important;

            .import-header {
                font-size: 1.38rem;
                color: $purple;
            }

            .content-api-label {
                margin-left: 0px;
                font-size: $subheader;
            }

            input {
                width: 100%;
                margin-left: 0px;
                border-radius: 11px;
            }

            .content-api-para {
                font-size: $normal-text;
                color: #9F9E9E;
                margin-top: 5px;

                span {
                    font-size: $normal-text;
                }
            }

            .content-api-anchor {
                text-decoration: none;
                color: $middle-grey;
            }

            button {
                width: 100%;
                background-color: $orange;
                border: none !important;
                padding: 7px;
                color: $white;
                font-size: 1.37rem;
                border-radius: 5px;
                margin-top: 10px;
                margin-bottom: 50px;

                &:hover {
                    color: $white !important;
                    background-color: $orange !important;
                }

                &:active {
                    color: $white !important;
                    background-color: $orange !important;
                }
            }

            .loader-connect {
                margin-left: 9rem;
                margin-bottom: 50px;
            }

            .loader-connect-s2s {
                margin-left: 9rem;
                margin-bottom: 50px;
            }
        }

        .next-row {
            display: none;

            .or {
                color: $purple;
            }
        }

        .oauth {
            border-right: $border-width;
            text-align: center;
            margin-top: 45px;
            margin-bottom: 45px;

            .oauth-button {
                width: 70%;
                background-color: $orange;
                border: none !important;
                padding: 7px;
                color: $white;
                font-size: 1.37rem;
                border-radius: 5px;
                margin-top: 10px;
                margin-bottom: 50px;

                &:hover {
                    color: $white !important;
                    background-color: $orange !important;
                }

                &:active {
                    color: $white !important;
                    background-color: $orange !important;
                }
            }

            .oauth-header {
                font-size: 1.38rem;
                color: $purple;
                margin-top: 20%;
                margin-left: 0px !important;
            }
        }
    }

    .sub-btn .btn {
        margin: 10px;
        padding: 8px;
        font-size: $subheader;
        width: 96px;
        color: $white;
        background-color: $orange !important;
        border-radius: 5px;
        border: none !important;
        margin-top: 30px;

        &:hover {
            color: $white !important;
            background-color: $orange !important;
        }

        &:active {
            color: $white !important;
            background-color: $orange !important;
        }
    }
}

@media screen and (max-width:773px) {
    .import {
        .main-contain {
            .body-change {
                text-align: center;
                display: inline-block !important;
                margin-top: 1rem;

                .body-header {
                    display: none;

                    .body-change-2 {
                        button {
                            font-size: $subheader;
                            margin-left: auto;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width:991px) {
    .import {
        .main-contain {
            .next-row {
                display: block;

                .hr {
                    display: block;
                }

                .or {
                    margin-top: -32px;
                    display: block;
                    width: 30px;
                    left: 50%;
                }
            }

            .oauth {
                border-right: none;
                margin-bottom: 0px;
            }

            .import-trades {
                .loader-oauth {
                    margin-bottom: 50px;
                    margin-left: 5.5rem;
                }
            }

            .content-api {
                .loader-connect-s2s {
                    margin-left: 10rem;
                    margin-bottom: 50px;
                }
            }
        }
    }
}

@media screen and (max-width:435px) {
    .import {
        .main-contain {
            .error-key {
                .error-para {
                    font-size: $normal-text;
                }
            }

            .body-change {
                padding-bottom: 10px !important;

                .body-header {
                    font-size: $subheader;
                }

                .body-change-2 {
                    button {
                        font-size: $subheader;
                    }
                }
            }

            .content-api {
                .content-api-label {
                    font-size: 18px;
                }

                .loader-connect {
                    margin-left: 6rem;
                    margin-bottom: 50px;
                }

                .loader-connect-s2s {
                    margin-left: 5.3rem;
                    margin-bottom: 50px;
                }
            }

            .content-section {
                .content-section-header {
                    font-size: $subheader;
                }
            }

            .import-trades {
                button {
                    font-size: $normal-text;
                    padding: 5px;
                }

                .loader-oauth {
                    margin-bottom: 50px;
                    margin-left: 3rem;
                }
            }
        }
    }
}