@import "../../../sass/shared/variable.scss";

.phone-verify-otp {

    .content-box {
        position: relative;
        border: 1px solid grey;
        background-color: $white;
        padding: 2rem;
        border-radius: 10px;

        .return-cross {
            position: absolute;
            top: -10px;
            right: -10px;
            cursor: pointer;
        }

        .content-box-heading {
            color: $purple;
            font-size: 2rem;
            margin: 0px 0px 25px 0px;

            span {
                color: $orange;
            }
        }

        .content-text {
            color: $purple;
        }

        button {
            margin: 15px 0px;
        }

        .btn-span-otp {
            margin-left: 50px;
            font-size: 18px;
        }

        .content-mobile-number {
            margin-top: -10px;
            margin-bottom: 30px;
        }

        .btn-green {
            background-color: $green;
            color: $black;
            font-weight: 500;
        }

        .form-group {
            .success {
                background: $success-msg-green;
                position: relative;
                color: $success-msg-green-border;
                width: 100%;
                border-radius: 10px;
                border: 1px solid $success-msg-green-border;
                padding: 0.3rem;
                margin-bottom: 10px;

                .success-cross {
                  position: absolute;
                  top: -5px;
                  right: -5px;
                  cursor: pointer;
                }
                .success-para {
                  word-wrap: break-word;
                  margin: 0px;
                }
              }
            .error2 {
                background: $error-msg-red;
                color: $error-msg-red-text;
                width: 100%;
                border-radius: 10px;
                border: 1px solid $error-msg-red-border;
                position: relative;
                padding: 7px 14px;
                margin-bottom: 10px;

                .error-para {
                    word-wrap: break-word;
                }

                .error-cross {
                    position: absolute;
                    top: -6px;
                    right: -6px;
                    cursor: pointer;
                }
            }

            input {
                width: 300px;
            }
        }

        .website-link {
            text-decoration: none;
        }
    }
}