@import  '../../../sass/shared/_common.scss';

.add-exchange-module{

  .main-contain {
    border: 1px solid $purple !important;
    border-radius: 13px;
    background-color: white;
    margin-bottom: 45px !important;
  }
  .module-main-head {
    padding-left: 8px !important;
    .header strong {
        color: $purple;
        margin-top: 40px;
      }
      strong span {
        color: $orange;
      }
  }

  .img-1 {
    position: relative;
    z-index: 1;
  }
  
  .img-2 {
    position: relative;
    z-index: 0;
    top: -2rem;
    left: 2rem;
  }
  @keyframes fame {
    25% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    }
    75% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  .img-slider {
    position: absolute;
    left: -5rem;
    top: -1.9rem;
    width: 18.6rem;
    height: 18.2rem;
    animation: anime 9s infinite;
    animation-timing-function: ease-in-out;
    background-repeat: no-repeat !important;
    -webkit-animation: anime 9s infinite;
    -webkit-animation-timing-function: ease-in-out;
  }
  @keyframes anime {
    0% {
        background: url($add-exchange-animation-image-1);
      }
    25% {
      background: url($add-exchange-animation-image-1);
    }
    50% {
      background: url($add-exchange-animation-image-2);
    }
    75% {
      background: url($add-exchange-animation-image-3);
    }
    100% {
      background: url($add-exchange-animation-image-4);
    }
  }
  
  .btn-div{
    text-align: center;
    .btn-app-primary {
      padding: 5px 18px;
      border-radius: 12px;
      font-size: $normal-text;
      color: $white;
      background-color: $purple;
      border: 1px solid $border;
    }
  } 

//Media Queries
// Small devices (landscape phones, 576px and up)
@media (min-width: 0) and (max-width: 575px) { 
  .img-slider{
    position: relative;
    margin: 0 auto;
    left:0;
    top:0;
  }
  .text-view{
    padding: 2rem;
  }
 }
// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767px) { 
  .img-slider{
    position: relative;
    margin: 0 auto;
    left:0;
    top:0;
  }
  .text-view{
    padding: 2rem;
  }
  
 }

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991px) { 
  .img-slider{
    position: relative;
    margin: 0 auto;
    left:0;
    top:0;
  }
  .text-view{
    padding: 2rem;
  }
 }

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199px) { 
  .img-slider{
    left: -2rem;
  }
 }

