@import "../shared/_common.scss";

.terms-of-service {
    @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
    *{
        box-sizing: border-box;
        font-family: 'Mukta', sans-serif;
    }
    .tos-banner {
        margin: 0;

        .tos-img {
            display: block;
        }

        .tos-banner-img {
            width: 25.313rem;
            height: 21.7rem;
            padding: 0;
            margin-top: 35px;
        }

        .tos-img-mobile {
            display: none;
        }

        .right-content-mobile {
            display: none;
        }

        .right-content {
            display: block;
            padding-left: 6rem;

            .tos-banner-content {
                padding: 0;
                width: 90%;
                height: 18.688rem;
                margin-top: 5.625rem;
                position: relative;

                .content-heading {
                    position: absolute;
                    color: $purple;
                    font-weight: 600;
                    top: 0;

                    .use-head {
                        color: $orange;
                    }
                }

                .banner-content {
                    position: absolute;
                    top: 77px;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 27px;
                    color: $tnc-text;
                }

               
            }
        }


    }

    .last-revised {
        color: $tnc-text;
        font-size: $big-text;
    }
    .tos-break {
        width: 100%;
        border: 1px solid #939393;
        margin: 50px 0px;
    }

    .tos-content {
        font-size:$big-text ;

        .content-head {
            color: $purple;
            font-size: 45px;
            font-weight: 600;

            .use {
                color: $orange;
            }
        }

        .bs {
            font-size: 30px;
            font-weight: 600;
        }

        .ss {
            font-weight: 600;
            font-size: 28px;
            text-transform:Capitalize !important;
        }
    }

    @media (max-width: 1200px) {
        .tos-banner {
            .right-content {
                .tos-banner-content {
                    .banner-content {
                        font-size: 16px;
                        line-height: 26px;
                    }

                    .last-revised {
                        font-size: 16px;
                    }
                }
            }
        }

        .tos-content {
            font-size: 16px;

            .content-head {
                font-size: 40px;
            }

            .bs {
                font-size: 25px;
            }
        }
    }

    @media (max-width: 994px) {
        .tos-banner {
            .right-content {
                padding-left: 4rem;

                .tos-banner-content {

                    height: 14.688rem;
                    margin-top: 7.625rem;

                    .banner-content {
                        font-size: 13px;
                        line-height: 21px;
                        top: 65px;
                    }

                    .last-revised {
                        font-size: 13px;
                    }
                }
            }
        }

        .tos-content {
            margin: 0px 2rem;
        }
    }

    @media (max-width: 770px) {
        .tos-banner {
            .tos-img {
                display: none;
            }

            .right-content {
                display: none;
            }

            .tos-img-mobile {
                display: block;

                .tos-banner-img {
                    padding: 0 3rem;
                    margin-top: 0;
                }
            }

            .right-content-mobile {
                display: block;

                .tos-banner-content {
                    .content-heading {
                        font-size: 35px;
                        color: $purple;
                        font-weight: 600;

                        .use-head {
                            color: $orange;
                        }
                    }

                    .banner-content {
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 27px;
                        color: $tnc-text;
                    }

                    .last-revised {
                        color: $tnc-text;
                        font-size: 16px;
                    }
                }
            }
        }

        .tos-content {
            font-size: $big-text;
            padding-left: 2rem;
            padding-right: 2rem;
            margin: 0;

            .content-head {
                font-size: 35px;
            }

            .bs {
                font-size: 25px;
            }
        }
    }
}