.user-address {
  @import "../../../sass/shared/variable.scss";

  * {
    box-sizing: border-box;
  }

  .active-btn {
    width: 100%;
    border: none;
    color: $white;
    padding: 8px 0px;
    font-size: 17px;
    font-weight: 500;
    border-left: 4px solid $orange;
    text-align: left;
    margin-bottom: 1px;
    background: $purple;
    cursor: default;
    padding-left: 5px;
  }

  .left-btns {
    width: 100%;
    border: none;
    color: $black;
    padding: 8px 0px;
    font-size: 17px;
    border-left: 4px solid #cfcddc;
    margin-bottom: 1px;
    text-align: left;
    padding-left: 5px;

    &:hover {
      text-decoration: underline;
    }
  }

  .icon-width-fixing {
    width: $header+6px;
    height: auto;
    text-align: center;
    display: inline-block;
  }

  .top-banner {
    background: #f7f7f7;
    width: 100%;
    padding: 20px 0px;
    border-radius: 10px 10px 0px 0px;
    position: relative;

    .cross-btn {
      position: absolute;
      right: -10px;
      top: -10px;
      cursor: pointer;
    }

    .settings-h1 {
      font-size: $header+8px;
      color: $purple;
      font-weight: 500;
      margin-bottom: 1rem;
    }

    .hello-user {
      margin-bottom: 0px;
      width: 380px;
      margin: auto;
      border: 1px solid $grey;
      font-weight: 500;
      color: $dark-blue;
      font-size: $header;
      background: $Whitish-grey;
      overflow: hidden;
      border-radius: 10px;
    }
  }

  .container-bg-user {
    background: url($map);
    background-position: center;
    background-size: cover;
    padding-top: 2rem;
    padding-bottom: 2rem;

  }
  .user-details-desktop {
    display: block;
  }

  .user-details-mobile {
    display: none;
  }

  .form-container-user {
    background: $white;
    border-radius: 10px;
    box-shadow: $box-shadow;
    width: 650px;
    margin: auto;


  }

  .bg-left-side {
    width: 100%;
    background: $white;

    height: 330px;
    color: $white;

    .bg-white-col {
      width: 100%;
      background: $white;
      height: 120px;

      .active-heading1 {
        width: 100% !important;
        margin: 0;
        padding: 0;
        background-color: $purple;
        color: $white;
        padding: 8px 0px;

        cursor: pointer;

        a {
          color: $white;
          text-decoration: none;
        }
      }

      .active-heading {
        width: 100%;
        text-align: center;
        color: $grey;
        cursor: pointer;
        border-bottom: 0.5px solid $grey;
        padding: 8px 0px;

        a {
          color: $black;
          text-decoration: none;
        }
      }
    }
  }

  .user-form {
    padding-top: 0;
    padding-bottom: 0;
    padding-right: 2rem;

  }
  .error-cross {
    position: absolute;
    top: -6px;
    right: -6px;
    cursor: pointer;
  }

  .error2 {
    background: $error-msg-red;
    color: $error-msg-red-text;
    width: 100%;
    border-radius: 10px;
    border: 1px solid $error-msg-red-border;
    position: relative;
    padding: 7px 14px;

    .error-para {
      word-wrap: break-word;
    }

    .error-cross {
      position: absolute;
      top: -6px;
      right: -6px;
      cursor: pointer;
    }
  }

  .error1 {
    background: #fcf5cb;
    color: #2c2c2c;
    width: 100%;
    border: 1px solid #ccc38a;
    border-radius: 10px;
    position: relative;
    padding: 0.3rem;

    &.margin-warning-top {
      margin-top: 12px;
    }

    &.margin-M-warning-top {
      margin-top: 3px;
    }
  }

  .success {
    background: $success-msg-green;
    position: relative;
    color: $success-msg-green-border;
    width: 100%;
    border-radius: 10px;
    border: 1px solid $success-msg-green-border;
    padding: 0.3rem;

    .success-cross {
      position: absolute;
      top: -5px;
      right: -5px;
      cursor: pointer;
    }

    .success-para {
      word-wrap: break-word;
      margin: 0px;
    }
  }

  .form-container-group {
    width: 50% !important;
    display: flex !important;
    display: inline-block !important;


    input {
      outline: 0;
      border: 0.7px solid rgb(170, 169, 169);
      width: 97%;
      margin-right: 5px;
      padding: 2px;
    }

    .select-box {
      outline: 0;
      border: 0.7px solid rgb(170, 169, 169);
      width: 97%;
      background: $white;
      border-radius: 8px;
      margin-right: 5px;
      padding: 4px;
      position: relative;
      top: -10px;
      margin-top: 10px;
    }
  }

  .text-heading {
    color: $purple;
    margin-bottom: 10px;
    font-size: $header;
  }

  .form-group input {
    border-radius: 8px;
    padding: 5px !important;
    margin-bottom: 7px;
  }

  .right-align-email {
    display: flex;
    align-items: center;
    justify-content: space-between;

    span a {
      font-size: $small-text;
      color: rgb(56, 172, 211);
      text-decoration: underline;
    }
  }

  .button-submit {
    background-color: $green;
    margin-top: 15px;
    margin-right: 10px;
    font-weight: 500;
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    text-decoration: none;

  }

  .bg-change {
    color: $black;
    background: rgb(218, 214, 214);
  }

  .form-input-label {
    font-size: $normal-text;
    color: $purple;
    margin: 1px 0px 4px 0px;
  }



  @media(max-width:767px) {
    .mobile-padding{
      padding: 1rem 2rem 0rem 2rem;
    }
    * {
      box-sizing: border-box !important;
    }

    .user-details-desktop {
      display: none !important;
    }

    .user-details-mobile {
      display: block !important;
      width: 100% !important;
    }

    .container-bg-user {
      background: $white;
      width: 100% !important;



      
      
      
    }
    .form-container-user {
      width: 100% !important;
      box-shadow: none;
      margin: 0px auto;

    }
    .padding-password {
      padding: 0px 10px;
      width: 100% !important;
      background: #f3f3f3;

      .left-btns {
        position: relative;
        width: 100% !important;
        background: #f3f3f3 !important;
        border: none !important;

        .right-icon {
          position: absolute;
          right: 20px;
          top: 20px;
        }
      }
    }

    .mobile-row {
      width: 100% !important;

      margin: 0;
      padding: 0;

      .active-btn {
        border: none;
        position: relative;
        width: 100% !important;

        .right-icon {
          position: absolute;
          right: $subheader;
          top: 15px;
        }
      }

      .left-btns {
        border-left: none !important;
        position: relative;
        width: 100% !important;

        .right-icon {
          position: absolute;
          right: $subheader;
          top: 15px;
        }
      }


    }

    .user-form {
      padding: 0px 30px !important;

      
      
    }
    .success {
      background: $success-msg-green;
      position: relative;
      color: $success-msg-green-border;
      width: 100%;
      border-radius: 10px;
      border: 1px solid $success-msg-green-border;
      padding: 0.3rem;

      .success-cross {
        position: absolute;
        top: -5px;
        right: -5px;
        cursor: pointer;
      }

      .success-para {
        word-wrap: break-word;
        margin: 0px;
      }
    }

    .top-banner {
      background: $white  !important;
      width: calc(100% - 2px) !important;
      padding: 20px 0px !important;
      border-radius: 10px 10px 0px 0px;
      position: relative;
      margin-bottom: -16px !important;
      margin-top: -30px;

      .cross-btn {
        display: none !important;
      }

      .settings-icon {
        width: 25px !important;
      }

      .settings-h1 {
        font-size: $header+2px;
        color: $purple;
        font-weight: 500;
        margin-bottom: 1rem !important;
      }

      .hello-user {
        margin-bottom: 0px;
        max-width: 380px;
        margin: auto;
        border: 1px solid $grey;
        font-weight: 500;
        color: #273251;
        font-size: 27px;
        background: #f3f3f3;
        overflow: hidden;
        border-radius: 10px;
      }
    }
  }

  @media(max-width:400px) {
    * {
      box-sizing: border-box;
    }

    .mobile-row {
      width: 120% !important;
    }

    .user-details-desktop {
      display: none !important;
    }

    .user-details-mobile {
      display: block !important;
    }

    .hello-user {
      margin-bottom: 0px;
      width: calc(100% - 1.5rem) !important;
      margin: auto;

    }
  }


}