@import '../../shared/_variable.scss';
$background-grad: linear-gradient(#FFFFFF, #F4F8FF) !important;

@mixin top-header () {
    font-size: $big-text;
    font-weight: 500;
}

.desktop-view {
    display: block;
}

.mobile-view {
    display: none;
}

.head-mobile {
    display: none;
}

.head-tab {
    display: block;
}

.main-container {
    margin-top: 4rem;

    .main-heading {
        color: $purple;
        font-size: $header;
        font-weight: 600;
    }

    .add-btn {
        color: $white;
        background-color: $orange;
        border-radius: 5px;
    }

    .my-btn {
        color: $orange;
        border: 1px solid $orange;
        border-radius: 5px;
    }

    .account-icon {
        font-size: $big-text;
    }

    .transactions {
        .back-purple {
            background-color: $purple;
            color: $white;
            border: 1px solid $border-grey;
            border-radius: 5px;
            height: 70px;

            .deposits-text {
                font-weight: 600;
                font-size: $normal-text;
            }

            .withdrawals-text {
                font-weight: 600;
                font-size: $normal-text;
            }

            .transaction-text {
                font-size: $big-text;
                font-weight: 600;
            }

            .trades-text {
                font-size: $normal-text;
                font-weight: 600;
            }

            .total-img {
                height: 37px !important;
                width: 37px !important;
                margin: auto;
                margin-top: auto;
            }
        }

        .total-transaction {
            border: 1px solid $border-grey;
            border-radius: 5px;
            background: $white;
            height: 70px;

            .transaction-text {
                font-size: $normal-text;
                font-weight: 600;
            }

            .total-img {
                height: 37px !important;
                width: 37px !important;
                margin: auto;
                margin-top: auto;
            }
        }

        .deposits {
            border: 1px solid $border-grey;
            border-radius: 5px;
            background: $white;
            height: 70px;

            .deposits-text {
                font-weight: 600;
                font-size: $normal-text;
            }

            .total-img {
                height: 40px;
                width: 40px;
                margin: auto;
                margin-top: auto;
            }
        }

        .withdrawals {
            border: 1px solid $border-grey;
            border-radius: 5px;
            background: $white;
            height: 70px;

            .withdrawals-text {
                font-weight: 600;
                font-size: $normal-text;
            }

            .total-img {
                height: 37px;
                width: 37px;
                margin: auto;
                margin-top: auto;
            }
        }

        .trades {
            border: 1px solid $border-grey;
            border-radius: 5px;
            background: $white;
            height: 70px;

            .trades-text {
                font-weight: 600;
                font-size: $normal-text;
            }

            .total-img {
                height: 37px;
                width: 37px;
                margin: auto;
                margin-top: auto;
            }
        }
    }

    .selecting-filters {
        position: relative;

        .dropdown {
            position: relative;

            .dropdown-btn {
                // font-weight: bold;
                padding: 10px 15px;
                font-size: $big-text;
                border: 1px solid $border-grey;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                cursor: pointer;

            }

            .dropdown-content {
                position: absolute;
                color: $purple;
                margin-top: 2px;
                width: 100%;
                border-radius: 5px;
                z-index: 1;
                background-color: white;

                .dropdown-item {
                    padding: 11px;
                    border-bottom: 1px solid $border-grey;
                    cursor: pointer;
                    font-size: $big-text;
                    display: flex;
                    justify-content: space-between;
                }
            }
        }

        .subtype-drop {
            height: 100px;
            overflow-y: scroll;
        }

        .value-input {
            border: 1px solid $border-grey;
            border-radius: 7px;
            font-size: $normal-text;
            color: $banner-para;
            padding: 6px 10px;
        }

        .searching-x{
            right: 25%;
            padding-top: 5px;
            .cross-image{
                height: 20px;
                width: 20px;
                cursor: pointer;
            }
        }
        .searching-date {
            right: 0;
            .btn-search {
                background-color: $orange;
                padding: 7px 14px;
                border: none;
                border-bottom-right-radius: 5px;
                border-top-right-radius: 5px;
                font-weight: 500;
                color: $white;
            }
        }

        .calendarElement {
            position: absolute;
            top: 40px;
            border: 1px solid black;
            z-index: 999;
            right: 6%;
            margin-top: 8px;
        }

        .rdrCalendarWrapper {
            .rdrMonths {
                .rdrMonth {
                    .rdrDays {
                        .rdrDay {
                            .rdrInRange {
                                background-color: transparent;
                            }

                            .rdrDayNumber {
                                color: black !important;

                                span {
                                    color: black !important;
                                }
                            }
                        }
                    }
                }
            }

            .rdrDateDisplayWrapper {
                .rdrDateDisplay {
                    .rdrDateDisplayItemActive {
                        color: $orange;
                        input {
                            color: $orange;
                        }
                    }
                }

            }
        }
    }

    .btn-reset{
            background-color: $orange;
            padding: 4px 14px;
            border: none;
            border-bottom-right-radius: 5px;
            border-top-right-radius: 5px;
            font-weight: 500;
            color: $white;
    }
    .note-para {
        font-size: $text;
        margin-bottom: 5px;

        .note-span {
            font-weight: 600;
        }
    }

    .modal-overley {
        position: fixed;
        width: calc(100% - 6rem);
        height: 100vh;
        background: rgba(255, 255, 255, 0.8);
        z-index: 8;

        .modal-btn {
            color: $white;
            background-color: $orange;
        }
    }

    .info-table-border {
        border-top: 1px solid $border;

        .sorting-icon{
            height: 17px;
            cursor: pointer;
        }
    }

    .static-border-bottom {
        position: sticky;
        top: 0;
        left: 0;
        right: 0;
        border-bottom: 1px solid $border;
    }

    th.border-adjustment {
        // padding: 0.6rem 0.2rem !important;
        font-size: $big-text;
        font-weight: 600 !important;
        border-bottom: 2px solid $border-grey;
    }

    .info-table-border table tbody tr td {
        padding: 0.7rem 0.4rem;
        font-size: $normal-text  !important;
        border-bottom: 1px solid $border-grey;
    }

    .limit-scroll {
        max-height: 50rem;
        min-height: 10rem;
        overflow: hidden;
        overflow-y: auto;

        .account-image {
            height: 26px;
            width: 26px;
        }

        .broken-robot {
            height: 500px;
            width: 500px;
            margin-left: 30%;
            margin-top: -5rem;
        }

        .loader-overley {
            position: absolute;
            width: calc(100% - 1rem);
            height: 50rem;
            background: rgba(255, 255, 255, 0.8);

            .sync-loader {
                filter: drop-shadow(0 0.5rem 0.5rem rgba(0, 0, 0, 0.2));
            }
        }

        .table-row-1 {
            background-color: $light-yellow-pending;
        }

        .trades-type {
            color: $purple;
            font-weight: 600 !important;
        }

        .currency-img {
            height: 24px;
            width: 24px;
        }

        .default-coin {
            height: 20px;
            width: 20px;
        }


    }
    .error-div-500{
        text-align: center;
        border-top: 1px solid $border-grey;
        padding-top: 50px;

        .error-heading{
            font-size: $bigger-head;
            font-weight: 600;
            margin-top: 30px;
            color: $purple;
        }
        .error-paragraph{
            font-size: $big-text;
            margin-top: 10px;
        }
        .error-button{
            color: $white;
            border-radius: 5px;
            background-color: $orange;
            margin-bottom: 90px;
        }
    }
}

@media (min-width: 768px) and (max-width: 992px) {
    .selecting-filters {
        position: relative;

        .calendarElement {
            position: absolute;
            top: 35px !important;
            border: 1px solid black;
            z-index: 999;
            right: 35% !important;
            margin-top: 8px !important;
        }

        .value-input {
            margin-left: 8rem;
        }
        .searching-x{
            right: 34% !important;
            padding-top: 5px;
            z-index: 9;
        }
        .searching-date {
            right: 20% !important;

            .btn-search {
                background-color: $orange;
                padding: 7px 14px;
                border: none;
                width: 60% !important;
                border-bottom-right-radius: 5px;
                border-top-right-radius: 5px;
                font-weight: 500;
                color: $white;
            }
        }
    }

    .info-table-border {
        .table-head-size {
            min-width: 62rem !important;
        }

        .limit-scroll {
            max-height: 50rem;
            min-height: 10rem;
            min-width: 62rem;
            overflow-x: auto;

            .account-image {
                height: 26px;
                width: 26px;
            }

            .table-size {
                min-width: 40rem !important;
            }
        }
    }
}

@media (min-width: 360px) and (max-width: 767px) {
    .desktop-view {
        display: none;
    }

    .mobile-view {
        display: block;
    }

    .main-container {
        min-height: 50rem;

        .head-mobile {
            display: block !important;
        }

        .head-tab {
            display: none !important;
        }

        .main-heading {
            font-size: $subheader;
            margin-top: 5px;
        }

        .account-img {
            height: 22px;
            width: 22px;
        }

        .add-btn {
            color: $white;
            background-color: $orange;
            border-radius: 5px;
        }

        .my-btn {
            color: $orange;
            border: 1px solid $orange;
            border-radius: 5px;
        }

        .account-icon {
            font-size: $normal-text;
        }

        .dropdown {
            position: relative;

            .dropdown-abc {
                padding: 10px 15px;
                font-size: $big-text;
                border: 1px solid $border-grey;
                border-radius: 5px;
                display: flex;
                color: $banner-para;
                align-items: center;
                justify-content: space-between;
                cursor: pointer;
            }

            .dropdown-btn {
                // font-weight: bold;
                padding: 10px 15px;
                font-size: $big-text;
                color: $purple;
                border: 1px solid $purple;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                cursor: pointer;

                .total-img {
                    height: 24px !important;
                    // width: 37px !important;
                    margin: auto;
                    margin-top: auto;
                }
            }

            .dropdown-content {
                position: absolute;
                color: $purple;
                border: 1px solid $purple;
                margin-top: 2px;
                width: 100%;
                border-radius: 5px;
                z-index: 99;
                background-color: white;

                .dropdown-item {
                    padding: 11px;
                    border-bottom: 1px solid $border-grey;
                    cursor: pointer;
                    font-size: $big-text;
                    color: $purple;
                    display: flex;
                    justify-content: space-between;

                    .dropwdown-img {
                        height: 25px;
                        width: auto;
                    }
                }
            }
        }
        .filter-btn{
            background-color: $top-footer-bg;
            border: #C9C9C9;
        }

        .special-note {
            font-size: $text;

            span {
                font-weight: 600;
            }
        }

        .filter-btn {
            border-radius: 5px !important;
            border: 1px solid $border-grey  !important;
            color: $black  !important;
        }

        .filtering-head {
            border: 1px solid $border-grey;
            background-color: $white;
            min-height: 300px;
            position: absolute;
            z-index: 9;
            border-radius: 5px;

            .apply-btn {
                margin-top: 10px;
                margin-bottom: 20px;
                margin-left: auto;
                color: $white;
                background-color: $orange;
                padding: 5px 15px;
                border-radius: 5px;
            }

            .type-filter {
                width: 90%;
            }

            .value-input {
                border: 1px solid $border-grey;
                border-radius: 5px;
                font-size: $big-text;
                color: $banner-para;
                padding: 6px 10px;
                width: 90%;
            }

           
            .searching-date {
                .btn-search {
                    background-color: $orange;
                    padding: 7px 11px;
                    border: none;
                    border-radius: 5px;
                    font-weight: 500;
                    color: $white;
                }
            }

            .image-search {
                margin-left: -30px;
                margin-top: 10px;
            }

            .calendarElement {
                top: 40px;
                z-index: 1;
                right: 6%;
                margin-top: 8px;
            }

            .rdrCalendarWrapper {
                .rdrMonths {
                    .rdrMonth {
                        .rdrDays {
                            .rdrDay {
                                .rdrInRange {
                                    background-color: transparent;
                                }

                                .rdrDayNumber {
                                    color: black !important;

                                    span {
                                        color: black !important;
                                    }
                                }
                            }
                        }
                    }
                }

                .rdrDateDisplayWrapper {
                    .rdrDateDisplay {
                        .rdrDateDisplayItemActive {
                            color: $white;
                            background-color: $orange;

                            input {
                                color: $white;
                            }
                        }
                    }

                }
            }

            .calender-date {
                padding-left: 0px !important;

                .rdrCalendarWrapper {
                    width: 97%;

                    .rdrMonths {
                        margin: auto;
                        padding-left: 0px;

                        .rdrMonth {
                            .rdrDays {
                                .rdrDay {
                                    .rdrInRange {
                                        background-color: transparent;
                                    }
                                    .rdrDayNumber {
                                        color: black !important;

                                        span {
                                            color: black !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .filter-container {
                width: 90%;

                .subtype-drop {
                    height: 80px;
                    overflow-y: scroll;
                }
            }
        }
        .error-div-500{
            text-align: center;
            border-top: 1px solid $border-grey;
            padding-top: 50px;
    
            .error-heading{
                font-size: $header;
                font-weight: 600;
                margin-top: 30px;
                color: $purple;
            }
            .error-paragraph{
                font-size: $normal-text;
                margin-top: 10px;
            }
            .error-button{
                font-weight: bold;
                margin-bottom: 0px !important;
            }
        }
    }

    #no-more-tables {
        max-height: 40rem;
        min-height: 10rem;
        border: none;

        .loader-overley {
            position: absolute;
            width: calc(100% - 1rem);
            height: 50rem;
            z-index: 8;
            background: rgba(255, 255, 255, 0.8);

            .sync-loader {
                filter: drop-shadow(0 0.5rem 0.5rem rgba(0, 0, 0, 0.2));
            }
        }

        tbody {
            display: block;
            position: relative;
            border: none;

            .change-color-pending {
                background-color: $light-yellow-pending;
            }
        }

        tr {
            display: block;
            border-bottom: 1px solid $middle-grey;

            .trades-type {
                color: $purple;
                font-weight: 600 !important;
            }
        }

        td {
            display: block;

            .currency-img {
                height: 24px;
                width: 24px;
            }
        }
    }

    .for-next {
        text-align: center;

        .next-btn {
            margin-top: 10px;
            margin-bottom: 20px;
            margin-left: auto;
            border: 2px solid $border-grey;
            padding: 5px 15px;
            border-radius: 5px;
        }
    }

    .error-div {
        border: 1px solid $error-msg-red-border;
        background-color: $error-msg-red;

        .cross-prog {
            top: 0px;
            left: 100%;
            float: right;
            margin-right: -10px !important;
            text-decoration: none;
            margin-top: -10px;
            cursor: pointer;
        }

        .image-div {
            .error-img {
                height: 26px;
                width: 33px;
            }

            .error-head {
                font-size: 16px;
                font-weight: 600;
                color: $error-msg-red-text;
            }
        }

        .error-para {
            font-size: 16px;
        }

        .try-again-error {
            .error-btn {
                color: $white;
                background-color: $orange;
            }
        }
    }
}