.reset-password {
    @import '../../../sass/shared/_variable.scss';
    @import '../../../sass/shared/common';
    .form-container {
        background: $white;
        padding: 30px;
        border-radius: 10px;
        margin-top: 15px !important;
        .header{
            font-size: $head-mobile;
            color: $purple;
            font-weight: bold;
        }
        .para{
            font-size: $big-text;
        }
    }
    .text-heading{
        font-size:$subheader+2px;
        font-weight: bold;
    }
    
    .text-paragraph{
        padding: 0px 25px;
        color: $banner-para;
        font-size: $subheader;
    }
    .login-link{
        color:$banner-para ;
        }
    .navbar-brand-img {
        padding-left: 30px;
    }

    .form-container h2 {
        font-size: $big-subheader;
        margin-bottom: 20px;
    }

    .form-container input {

        border-radius: 5px;
    }

    label {
        font-size: $big-text;
        margin: 0;
    }
    .input-size{
        font-size: $big-text;
    }
    .btn:hover {
        color: $white;
    }
    .btnform {
        border-radius: 5px;
        cursor: pointer !important;
        background-color: $orange !important;
        color: $white;
        margin-bottom: 20px !important;
        font-weight: 500;
        font-size: $big-text;
        border: none;
        outline: none !important;
    }

    .back {
        color: $purple !important;
        margin-top: 10px;
        text-decoration: none;
        font-size: $sub-text;
    }

    h5 {
        font-size: $text+1px;
    }

    span {
        color: red;
    }

    .btn-success {
        background-color: #bfe0b9;
        color: rgb(32, 31, 31);

        border: none;
    }

    .pointerHover {
        cursor: pointer;
        font-size: $big-text;
        text-decoration: underline;

    }

    @media(max-width:768px) {
        .navbar-brand img {
            width: 240px !important;
            height: 40px !important;
        }
    }

    #span {
        width: 80px !important;
        height: 20px !important;
        text-align: center;
        margin-left: 40%;
        margin-top: 20px;
    }
}