@import "../shared/_common.scss";
.privacy-policy {
    @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
*{
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
}
    .privacy-banner {
        padding-left: 77px;

        .banner-img {
            margin-top: 63px;
            display: block;

            .privacy-img {
                width: 497.63px;
                height: 294px;
            }
        }

        .banner-img-mobile {
            display: none;
        }

        .banner-right {
            display: block;
            margin-top: 63px;
            margin-top: 63px;
            padding-top: 40px;
            padding-left: 71px;

            .privacy-banner-content {
                width: 100%;
                height: 250px;
                position: relative;

                .banner-heading {
                    position: absolute;
                    color: $purple;
                    font-weight: 600;
                    top: 0;
                    margin: 0;

                    .privacy {
                        color: $orange;
                    }
                }

                .banner-content {
                    position: absolute;
                    top: 86px;
                    font-weight: 600;
                    line-height: 27px;
                    color: $tnc-text;
                }

                .note {
                    position: absolute;
                    color: $tnc-text;
                    font-weight: 600;
                    bottom: 10px;
                }

                .revised {
                    font-size: $big-text;
                    color: $tnc-text;
                }
            }
        }

        .banner-right-mobile {
            display: none;
        }


    }

    .line-break {
        margin: 75px 0px;
        margin-bottom: 40px;
        color: $middle-grey;
    }

    .terms-policies {
        margin: 0px 106px;

        .title {
            color: $purple;
            font-weight: 600;

            .policy {
                color: $orange;
            }
        }

        .main-content {
            color: $tnc-text;
            font-size: $big-text;
            margin-top: 30px;

            .small-subheading {
                margin: 0;
                font-weight: 600;
            }

            .big-subheading {
                margin: 0;
                font-weight: 600;
                font-size: $medium-subheader;
            }
        }
    }



    @media (max-width:1236px) {
        .privacy-banner {
            .banner-right {
                .privacy-banner-content {

                    height: 215px;

                    .banner-content {
                        top: 75px;
                    }

                    .note {
                        bottom: 4px;
                    }

                }
            }
        }

        .line-break {
            margin: 25px 0px;
        }

        .terms-policies {
            margin: 0px 92px;

            .main-content {
                margin-top: 27px;
            }
        }

    }

    @media (max-width:1010px) {
        .privacy-banner {

            .banner-img {
                margin-top: 0;
            }

            .banner-right {

                margin-top: 38px;
                padding-left: 50px;

                .privacy-banner-content {

                    height: 140px;

                    .banner-heading {
                        font-size: $header;
                    }

                    .banner-content {
                        line-height: 17px;
                        top: 53px;
                    }

                    .note {
                        bottom: -3px;
                    }

                }
            }
        }

        .line-break {
            margin-top: 0;
            margin-bottom: 30px;
        }

        .terms-policies {
            margin: 0px 90px;
            .main-content {
                margin-top: 20px;
            }
        }

    }

    @media (max-width:770px) {
        .privacy-banner {

            padding-left: 29px;

            .banner-img {
                display: none;
            }

            .banner-img-mobile {
                display: block;
                padding: 0;
                margin-top: 10px;

                .privacy-img-mobile {
                    width: 50%;
                }
            }

            .banner-right {
                display: none;
            }

            .banner-right-mobile {
                display: block;
                padding: 0;
                padding-top: 23px;


                .privacy-banner-content-mobile {
                    text-align: center;

                    .banner-heading-mobile {
                        color: $purple;
                        font-weight: 600;
                        font-size: $header;
                      
                        .privacy-mobile {
                            color: $orange;
                        }
                    }

                    .banner-content-mobile {
                        font-size: $big-text;
                        color: $tnc-text;
                    }

                    .revised-mobile {
                        font-size: $big-text;
                        color: $tnc-text;
                
                    }
                }
            }
        }

        .line-break {
            margin: 20px 0px;
        }

        .terms-policies {
            margin: 0px 16px;
            .main-content {
                font-size: $big-text;
                margin-top: 15px;

                .big-subheading {
                    font-size: $big-text;
                }
            }
        }
    }
}