.email-success-page{
    @import '../../../sass/shared/variable.scss';
.container-fluid{
    background-position: center;
    background-size: cover;
    height: 600px;
    width: 100%;
    .form-container{
        background: $white;
        padding:15px ;
        border-radius: 10px;
        box-shadow: 0px 0px 5px 0px rgb(34, 33, 33);
        margin-top: 150px!important;
        border-left: 12px solid #8AD088 !important;
        .form-container-head{
            margin-bottom: 3px ;
            font-size: 24px;
            margin-bottom: 20px;
            color: rgb(70, 68, 68);
        }
        .form-container-img{
            height:80px;
            width: 80px;
        margin-top: 35px;
        }
        .btnform{
            border-radius: 25px;
            margin-top: 7px;
            padding: 5px 10px !important;
            font-weight: 500;
            border: none;
        }
        .btn-bg{
            background-color: #bfe0b9;
        }
    }

}

}