.user-details {
  @import "../../shared/common";

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }

  @media(min-width:768px) {
    .background-map {
      background: url($map);
      background-position: center;
      background-size: cover;
      padding-top: 2rem;
      padding-bottom: 2rem;

      .form-div-container {
        background: $white;
        border-radius: 10px;
        box-shadow: $box-shadow;
        width: 650px;
        margin: auto;
        padding: 0px;

        .top-banner {
          background: #f7f7f7;
          width: 100%;
          padding: 20px 0px;
          border-radius: 10px 10px 0px 0px;
          position: relative;

          .cross-btn {
            position: absolute;
            right: -10px;
            top: -10px;
            cursor: pointer;



          }

          .settings-h1 {
            font-size: $head-mobile;
            color: $purple;
            font-weight: 500;
          }

          .hello-user {
            margin-bottom: 0px;
            width: 380px;
            margin: auto;
            border: 1px solid $grey;
            font-weight: 500;
            color: #273251;
            font-size: $header;
            background: #f3f3f3;
            overflow: hidden;
            border-radius: 10px;
          }
        }

        .active-btn {
          width: 100%;
          border: none;
          color: $white;
          padding: 8px 0px;
          font-size: $big-text;
          font-weight: 500;
          border-left: 4px solid $orange;
          text-align: left;
          margin-bottom: 1px;
          background: $purple;
          cursor: default;
          padding-left: 5px;
        }

        .left-btns {
          width: 100%;
          border: none;
          color: #333333;
          padding: 8px 0px;
          font-size: $big-text;
          border-left: 4px solid #cfcddc;
          margin-bottom: 1px;
          text-align: left;
          padding-left: 5px;

          &:hover {
            text-decoration: underline;
          }
        }

        .user-info-h1 {
          font-size: $semi-big-header;
          color: $purple;
          font-weight: 500;
        }

        .button-submit {
          border-radius: 10px;
          background: $green;
          font-size: $normal-text;
          border: none;
          padding: 10px 18px;


        }

        .disableCursor {
          cursor: not-allowed;
        }

        .padding-country {
          padding: 0px;
          padding-left: 12px;

          .react-tel-input {
            padding: 0;
            width: 1px;

            .form-control {
              width: 1px;
              padding: 0px;
              height: 30px;
              display: none;
            }
          }
        }

        .phone {
          width: calc(100%);
        }

        .error-cross {
          position: absolute;
          top: -6px;
          right: -6px;
          cursor: pointer;
        }

        .error2 {
          background: $error-msg-red;
          color: $error-msg-red-text;
          width: 100%;
          border-radius: 10px;
          border: 1px solid $error-msg-red-border;
          position: relative;
          padding: 7px 14px;

          .error-para {
            word-wrap: break-word;
          }

          .error-cross {
            position: absolute;
            top: -6px;
            right: -6px;
            cursor: pointer;
          }
        }

        .error1 {
          background: #fcf5cb;
          color: #2c2c2c;
          width: 100%;
          border: 1px solid #ccc38a;
          border-radius: 10px;
          position: relative;
          padding: 0.3rem;

          &.margin-warning-top {
            margin-top: 12px;
          }

          &.margin-M-warning-top {
            margin-top: 3px;
          }

          .info-div {
            padding: 1rem;
          }

          .info-list {
            margin: 0;
            padding-left: 1rem;
          }

          .info-heading {
            font-size: $big-text;
            font-weight: 600;
            margin-bottom: 0.5rem;
          }

          .error-triangle {
            position: absolute;
            top: -13px;
            left: 10px;
          }

          .error-M-triangle {
            position: absolute;
            top: -13px;
            left: 21px;
          }

          .error-para {
            padding: 5px;
            word-wrap: break-word;
          }
        }

        .right-align-email {
          display: flex;
          align-items: center;
          justify-content: space-between;
          position: relative;

          span a {
            font-size: $small-text;
            color: rgb(56, 172, 211);
            text-decoration: underline;
          }
        }

        .text-not-verified {
          font-size: $small-text;
          position: absolute;
          top: 37px;
          right: 14px;
          font-weight: 700;
          z-index: 10;
          color: #C15358;
          width: 5.7rem;
        }

        .text-verified {
          font-size: $small-text;
          position: absolute;
          top: 39px;
          right: 14px;
          font-weight: 700;
          z-index: 10;
          color: #489332;
        }

        .icon-verify {
          margin-bottom: 3px;
          width: 15px;
        }

        .text-error {
          font-size: $text;
        }

        .success {
          background: $success-msg-green;
          position: relative;
          color: $success-msg-green-border;
          width: 100%;
          border-radius: 10px;
          border: 1px solid $success-msg-green-border;
          padding: 0.3rem;

          .success-triangle {
            position: absolute;
            top: -13px;
            left: 10px;
          }

          .success-M-triangle {
            position: absolute;
            top: -13px;
            left: 21px;
          }

          &.success-margin {
            margin-top: 12px;
          }

          &.success-M-margin {
            margin-top: 3px;
          }

          .success-cross {
            position: absolute;
            top: -5px;
            right: -5px;
            cursor: pointer;
          }

          .success-para {
            word-wrap: break-word;
            margin: 0px;
          }
        }
      }
    }

    .user-information-container {
      padding-right: 2rem;

      .form-input-label {
        margin-top: 1px;
        margin-bottom: 2px;
        font-size: $big-text;
        color: $purple;
      }

      .form-input {
        width: 100%;
        padding: 0.3rem 0.6rem;
        border: 1px solid $border;
        border-radius: 10px;
        font-size: $normal-text;
        position: relative;
      }

      input[type="date"]::-webkit-calendar-picker-indicator {
        background: transparent;
        bottom: 0;
        color: transparent;
        cursor: pointer;
        height: auto;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        width: auto;
        z-index: 9999;
      }

      .calendar-div {
        position: relative;

        .calendar-icon {
          position: absolute;
          right: 1rem;
          top: 0.5rem;
          z-index: 1;
        }
      }

      div.flag-dropdown {
        border-radius: 10px !important;
        height: 35px;
        border: 1px solid $border;

        &:hover {
          background: $border  !important;
          border-radius: 10px !important;
          height: 35px !important;
          border: 1px solid $border  !important;
        }

        .selected-flag {
          border-radius: 10px;
        }
      }
    }

    .icon-width-fixing {
      width: 30px;
      height: auto;
      text-align: center;
      display: inline-block;
    }

    .container-bg-user {
      background: url($map);
      background-position: center;
      background-size: cover;
      height: 110vh;
      padding-top: 50px !important;
      padding-bottom: 50px !important;

      .form-container-user {
        background: $white;
        border-radius: 10px;
        box-shadow: $box-shadow;

        width: 650px;

        height: 100vh;
        margin: auto;

        .jumbotron {
          height: 150px;
          padding: 20px 0px;

          width: 100%;
          border-radius: 12px 12px 0px 0px;
          background: #f7f7f7;

          h1 {
            font-size: $semi-big-header;
            color: $purple;
            font-weight: 500;
          }

          .button-hello {
            padding: 0px 150px !important;
            border: 1px solid $grey  !important;
            font-weight: 400 !important;
            color: $grey  !important;
            font-size: $big-subheader  !important;
          }
        }

        .bg-left-side {
          width: 100%;
          background: #f6f6f6;

          height: 330px;
          color: white;

          .bg-white-col {
            width: 100%;
            background: #f6f6f6;
            height: 120px;

            .active-heading1 {
              width: 100% !important;
              margin: 0;
              padding: 0;
              background-color: $purple;
              color: $white;
              padding: 8px 0px;

              cursor: pointer;

              a {
                color: white;
                text-decoration: none;
              }
            }

            .active-heading {
              width: 100%;
              text-align: center;
              color: $grey;
              cursor: pointer;
              border-bottom: 0.5px solid $grey;
              padding: 8px 0px;

              a {
                color: $black;
                text-decoration: none;
              }
            }
          }
        }

        .user-form {
          padding-top: 0;
          padding-bottom: 0;

          .form-container-group {
            width: 50% !important;
            display: flex !important;
            display: inline-block !important;

            input {
              outline: 0;
              border: 0.7px solid rgb(170, 169, 169);
              width: 97%;
              margin-right: 5px;
              padding: 2px;
            }
          }

          .text-heading {
            color: $purple;
            margin-bottom: 10px;
            font-size: $header;
          }

          .form-group input {
            border-radius: 8px;
            padding: 5px !important;
            margin-bottom: 7px;
          }

          .button-submit {
            background-color: $green;
            margin-top: 15px;
            margin-right: 10px;
            font-weight: 500;
            border: none;
            border-radius: 10px;
            padding: 5px 10px;
            text-decoration: none;
            text-decoration: none;
          }

          .bg-change {
            color: black;
            background: rgb(218, 214, 214);
          }
        }
      }
    }

    .right {
      position: relative;
      z-index: 1;
    }

    .loader-size {
      width: 35px;
    }
  }

  @media(max-width:767.5px) {
    input[type="date"] {
      display: block;
      -webkit-appearance: none;
      -moz-appearance: none;
      background: $white;
      min-height: 35.6px;
    }

    .settings-h1 {
