.signup-success {
    @import '../../sass/shared/variable.scss';

    .container-fluid {
        background-image: url($map);
        background-position: center;
        background-size: cover;
        height: 100vh;

        .form-container {
            background: $white;
            padding: 30px;
            border-radius: 10px;
            box-shadow: $box-shadow;
            margin-top: 15px !important;

            .form-container-head {
                font-size: 27px;
                margin-bottom: 10px;
               color: $purple;
            }

            .form-container-para {
                font-size: 18px;
                color: $grey;
                font-weight: bold;
            }

           

            .btn-submit {
                border-radius: 12px;
                margin-top: 20px;
                background-color: $green;
                color: $black;
                font-weight: 500;
                border: none;
            }
        }

        .navbar-img {
            width: 300px;
            height: 50px;
        }
    }




    @media(max-width:768px) {
        .navbar-brand img {
            width: 200px !important;
            height: 40px !important;
        }
    }

}