@import '../../shared/_variable.scss';
$background-grad:linear-gradient(#FFFFFF,#F4F8FF) !important;

@mixin error2($backcolor:#FFF0F2,$bordercolor:1px solid #FFDBE0,$margin:10px,$width:99%) {
    font-size:$normal-text;
    font-weight: 400;
    border-radius: 13px;
    height:auto;
    width: $width;
    padding-bottom: 0px !important;
    background-color: $backcolor; 
    border:$bordercolor !important;
    margin-left: $margin;
}
.go-back{
    margin-bottom:30px;
}
.go-back-img{
    height: 20px;
    width: auto;
    margin-bottom: 5px;
}
.go-back-a{
    font-size: 19px;
}
div.first-part{
    margin:0px auto;
    .loader-account{
        border: 1px solid $border;
        border-radius: 13px;
        height: 285px;
        text-align: center;
        margin-top: 10px;
        background: $background-grad;
        .loader-image{
            margin:auto;
            margin-top: 7rem;
        }
    }
    .main-heading{
        font-size: $header;
        color: $purple;
        font-weight: bold;
        .main-para{
            font-size: $big-text;
            line-height: normal;
        }
    }
    .no-account{
        border: 1px solid $border;
        border-radius: 13px;
        height: 285px;
        text-align: center;
        background: $background-grad;
        .expired-img{
            margin:auto;
            margin-top: 6rem;
        }
        .expired-para{
            left: 26%;
            margin-left:auto;
            font-size: $subheader;
            span{
                font-weight: 600;
            }
        }
    }
    .expired-accounts{
        border: 2px solid $failure;
        border-radius: 13px;
        padding-top: 1rem;
        min-height: 285px;
        max-height: 285px;
        overflow-y: scroll;
        .expired-list{
            display: flex;
            flex-direction: row;
            border-bottom:2px dotted $border;
            margin-top: 10px;
            .account-name{
                .light-color{
                    line-height: 0;
                    font-size: $text;
                    color: $border;
                }
                .para-account{
                    font-size:$big-text;
                    font-weight: 600;
                    .inprog-account-img{
                        padding-right: 5px;
                    }
                }
                .info-icon-adjustment{
                    position: relative;
                    top:-2px;
                    .user-portfolio-info-icon{
                        width: 1rem;
                        height:1rem;
                        position: relative;
                        top:0px;
                    }
                }
                .delete-icon{
                    margin-left: auto;
                    cursor: pointer;
                }
                .syncing-connected{
                    display:none;
                }
            }
        }
        .error-connected{
            margin-top: -25px;

        .error-connect-img{
            padding-left:35px;
            padding-bottom: 0px;
            padding-top: 0px;
            margin-top: 9px;
        }
        .error2{
            @include error2;
             .error2-para{
                 color: #B5747E;
                 line-height: normal;
                 padding-top: 10px;
                 span{
                     color: #8C0858;
                     font-weight: bold;
                 }
             }
         }
         .error-2-warning{
            align-items: center;
            margin-left: 0px;
            height:auto;
            margin-bottom: 10px;

            .cross-connected{
                top: 0px;
                left: 100%; 
                float: right;
                margin-right: -27px !important;
                text-decoration: none;
                margin-top: -12px;
                cursor: pointer;
            }
            .error-2-img{
                cursor: pointer;
                color: $purple;
                text-decoration: underline;
            }
        }}
    }
}
