div.third-part{
    // width: calc(100% - 1rem);
    margin:0px auto;
    margin-top: 40px;
    .main-heading-mobile{
        display: none;
    }
    .sync-mobile{
        display: none;
    }
    .main-sync{
        display:flex;
        justify-content: space-between;
        .sync-all{
            background-color: $purple;
            color: white;
            border: none;
            height: 32px;
            width: 8rem;
            }
            .main-heading{
                font-size: $header;
                color: $purple;
                font-weight: bold;
            }
    }
    .loader-account{
        border: 1px solid $border;
        border-radius: 13px;
        height: 285px;
        text-align: center;
        margin-top: 10px;
        background: $background-grad;
        .loader-image{
            margin:auto;
            margin-top: 7rem;
        }
    }
    .no-account{
        border: 1px solid $border;
        border-radius: 13px;
        height: 285px;
        text-align: center;
        margin-top: 10px;
        background: $background-grad;
        .complete-img{
            margin:auto;
            margin-top: 6rem;
        }
        .complete-para{
            left: 26%;
            margin-left:auto;
            font-size: $subheader;
            span{
                font-weight: 600;
            }
        }
    }
    .connected-accounts{
        border: 2px solid #489332;
        border-radius: 13px;
        margin-top: 10px;
        padding-top:4px;
        max-height: 285px;
        min-height: 285px;
        overflow-y: scroll;
        .connected-list{
            display: flex;
            flex-direction: row;
            border-bottom:1px dashed $border;
            .account-name{
                .light-color{
                    line-height: 0;
                    font-size: $text;
                    color: $border;
                }
                .right-image{
                    display: inline;
                }
                .coins-img{
                    width: 32px;
                    height: 32px;
                }
                .length{
                    border: 1px solid black;
                    border-radius: 50%;
                    padding: 5px 7px;
                    cursor: pointer;
                    display: inline-block;
                    margin-top: -2px;
                }
                .add-date{
                    line-height:0;
                    font-size: $text;
                    color: $border;
                    padding-left: 35px;
                    margin-top: -10px;
                }
                .right-img{
                    padding-left: 5px;
                    padding-right: 10px;
                    display: inline;
                }
                .para-account{
                    font-size:$big-text;
                    font-weight:600;
                    .inprog-account-img{
                        padding-right: 5px;
                    }
                }
                .info-icon-adjustment{
                    position: relative;
                    top:-2px;
                    .error-tip{
                        background-color: $dark-red !important;
                        fill:$dark-red !important;
                    }
                    .syncing-error{
                        position: relative;
                        top:0px;
                        fill:$dark-red;
                    }
                }
                .syncing-refresh{
                    display: none;
                }
                .refresh-icon{
                    cursor: pointer;
                    padding-right: 10px;
                }
                .syncing-connected{
                    display:none;
                }
                .delete-icon{
                    margin-left: auto;
                    padding-left: 10px;
                    cursor: pointer;
                }
            }
            .error-fetch{
                display: none !important;
            }
            .all-coins{
                margin-bottom: 20px !important;
                border-radius: 13px;
                background-color: white;
                width: 90%;
                margin: auto;
                .cross-connected{
                    top: 0px;
                    left: 100%; 
                    float: right;
                    margin-top: -12px;
                    margin-right: -39px !important;
                    text-decoration: none;
                    // margin-top: -12px;
                    cursor: pointer;
                }
                .main-coins-div{
                    display: inline-block;
                    .coins-para{
                        display: inline-block;
                        margin-bottom: 0px;
                        padding: 0.3rem;
                        margin: auto;
                        .coins-total-img{
                            height: 24px;
                            width: 24px;
                        }
                        .default-coin{
                            height: 18px;
                            width: 18px; 
                        }
                        .coins-span{
                            font-size: $text;
                        }
                    }
                }
            }
            .error-connected{
                margin-top: -10px;
    
            .error-connect-img{
                padding-left:35px;
                padding-bottom: 0px;
                padding-top: 0px;
                margin-top: 9px;
            }
            .error2{
                @include error2($width:99%);
                 .error2-para{
                     color: #B5747E;
                     line-height: normal;
                     padding-top: 10px;
                     span{
                         color: #8C0858;
                         font-weight: bold;
                     }
                 }
             }
             .error-2-warning{
                align-items: center;
                margin-left: 0px;
                height:auto;
                margin-bottom: 10px;
    
                .cross-connected{
                    top: 0px;
                    left: 100%; 
                    float: right;
                    margin-right: -27px !important;
                    text-decoration: none;
                    margin-top: -12px;
                    cursor: pointer;
                }
                .error-2-img{
                    cursor: pointer;
                    color: $purple;
                    text-decoration: underline;
                }
            }}
        }
    }
}
.accounts{
    overflow-x: hidden;
}
@media screen and (max-width:991px){
    .go-back{
        display: flex;
        justify-content: space-between;
    }
    .third-part{
        .connected-accounts{
            .connected-list{
                .second-thing{
                    margin-top: 20px;
                }
                .error-fetch{
                    display:inline-block;
                }
            }
        }
    }
}
@media screen and (max-width:770px) {
    .third-part{
        .mobile-head{
            .main-heading-mobile{
                display: inline !important;
                font-size: $subheader;
                color: $purple;
                font-weight: bold;
            }
            .sync-mobile{
                display: inline !important;
            }
        }
        .main-sync{
            display: block !important;
            .main-heading{
                display: none;
            }
            .sync-desktop{
                display: none;
            }
            .main-filter{
                justify-content: space-between !important;
                .filter-btn{
                    font-size: 14px;
                }
            }
        }
    }
}
@media screen and (max-width:490px) {
    .second-part{
        .inprogress-accounts{
            min-height: 180px !important;
        }
        .main-heading{
            margin-top: 20px;
        }
    }
}
@media screen and (max-width:430px) {
    .third-part{
        .connected-accounts{
            .connected-list{
                .all-coins{
                    .main-coins-div{
                        .coins-para{
                            margin: 0px;
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width:395px) {
    .go-back{
        .go-back-a{
            font-size: $normal-text;
        }
    }
    .third-part{
        .main-sync{
            .sync-all{
                padding:0px 7px !important;
            }
        }
    }
}
