@import "./_common.scss";

.footer {

    //DESKTOP VIEW
    .social-heading {
        color: $black !important;
        font-size: 1.50rem;
        margin-right: 15px !important;
        font-weight: bold;
    }

    .social-inline {
        display: inline;
    }
    .top-section {
       padding-top: 10px !important;
    }
    .social-icon-margin {
        height: 30px;
        width: 30px;
        margin-right: 15px !important;
        margin-top: -10px !important;
    }

    .read-more-btn {
        font-size: $big-text !important;
        color: $white;
        text-decoration: none;
    }

    .footer-heading-text {
        margin-left: -30px;
        list-style: none;

        .footer-heading-list {
            margin-top: 15px;

            &::before {
                content: "\2014";
                padding-right: 15px;

            }

            .footer-heading-link {
                text-decoration: none;
                color: $black;
                font-size: $big-text;
                margin-bottom: 30px;
            }
        }

    }

    .footer-box {
        position: relative;
        height: 13.938rem;
        width: 19.875rem !important;
        margin: auto;
        margin-top: -17px !important;
        margin-bottom: 2.5rem;
    }

    .footer-mail {
        position: absolute;
        font-size: $subheader;
        font-weight: bold;
        bottom: 3rem;
        left: 4.3rem;
        color: $purple;

        span {
            color: $orange;
        }
    }


    .footer-band {
        background-color: $down-footer-bg;

        .band-para {
            color: $white;
            font-size: $big-text;
            margin: 0px;
            padding: 10px 0px;
        }

        .band-image {
            height: 32px;
            width: 32px;
            cursor: pointer;
        }

        .top-scroll {
            padding-right: 6rem;
        }

    }

    .footer-bg-map {
        background: $top-footer-bg;
        background-size: cover;
        background-position: center;
        position: relative;
        padding-top: 60px;
    }

    .footer-box-image {
        position: absolute;
        top: 15px;
        left: 50%;
        transform: translate(-50%, -50%);
    }


    .get-started-btn {
        background-color: $orange;
        border: 0;
        padding: 0.5rem 1rem;
        color: $white;
        text-decoration: none;
        font-size: $subheader;
        font-weight: 600;
        border-radius: 5px;
        cursor: pointer;
        margin-left: 0;

        &:hover {
            color: $white;
            background-color: $orange;
        }
    }


    .upper-banner-header {
        font-size: $big-header !important;
        color: $black;
        font-weight: bold;
        margin-bottom: 40px;
    }

    .upper-img-div {
        padding-top: 3.5rem;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .upper-banner-img {
            padding-left: 1.5rem;
            padding-right: 4.5rem;

        }
    }


    .content-heading {
        font-weight: bold;
    }

    .padding-left-content {
        .footer-heading-text {
            list-style: none;

            .footer-heading-list {
                .footer-heading-link {
                    text-decoration: none;
                    color: $black;
                    font-size: $big-text;
                }
            }
        }
    }

    .lower-banner {
        background-repeat: no-repeat;
        background-size: cover;
        height: 10.8rem;
        margin: 2rem 0rem;
        position: relative;

        .lower-banner-header {
            font-size: $big-header;
            font-weight: bold;
            color: $black;
            margin-bottom: 0;
        }

        .lower-banner-para {
            color: $black;
            font-size: $subheader;
            margin-bottom: 1rem;
            margin-top: 30px;

            span {
                font-size: $big-text;

            }
        }

        .div-center {
            position: absolute;
            top: 55%;
            left: 55%;
            transform: translate(-50%, -55%);
            width: 100%;
        }
    }

    @media(max-width:1200px) {
        .social-icon-margin {
            margin-right: 2px !important;
        }
    }
    .footer-paragraph{
        font-size: $big-text;
    }
    @media(max-width:992px) {
        .padding-left-content {
            margin-left: 10px;
            padding-bottom: 30px;
        }

        .top-section {
            text-align: left !important;
            margin-left: 27px;
            box-sizing: border-box;

            img {
                height: 20px;
            }
        }

        .lower-banner {
            background-repeat: no-repeat;
            background-size: cover;
            height: 12rem;
            padding-top: 5px;
            margin-left: 35px !important;
        }

        .lower-banner-header {
            font-size: $big-subheader !important;
            font-weight: 500;
            color: $black;
            margin-bottom: 0;

        }

        .lower-banner-para {
            color: $black;
            font-size: $big-text !important;
            margin-bottom: 1rem;
            margin-top: 30px;
        }

        .margin-image {
            margin-top: 28px;
        }

        .upper-banner-header {
            font-size: $big-subheader !important;
            margin-bottom: 30px;
        }

        .footer-bg-map {
            position: relative;
        }

        .footer-box-image {
            position: absolute;
            width: 50px !important;
            height: 25px !important;
            left: 50% !important;
            top: 10px;
            transform: translate(-50%, -50%);
        }

        .social-heading {
            color: $black !important;
            font-size: $header !important;
            font-weight: bold;
            text-align: left;
        }

        .social-inline {
            display: block;
            margin-top: 20px;
            margin-bottom: 30px;
        }

        .flex-content {
            display: flex;
        }

        .social-icon-section {
            margin-left: 2px;
        }

        .footer-right {
            margin-left: 15px;
        }

        .social-icon-margin {
            margin-left: 2px;
            margin-right: 15px !important;
        }

        .footer-paragraph {
            font-size: $normal-text !important;
            margin-top: 10px;   
        }

        .footer-heading-text {
            margin-left: -30px;
            list-style: none;

            .footer-heading-list .footer-heading-link {
                font-size: $normal-text !important;
                margin-bottom: 3px !important;
            }

        }

        .read-more-btn {
            font-size: $normal-text !important;
            box-sizing: border-box !important;
            margin-bottom: 30px;

        }

        .get-started-btn {
            display: inline-block;
            text-align: left;
            margin-left: 0px;
            margin-top: 20px;
            font-size: $normal-text;

        }

        .upper-banner-img {
            padding-left: 0rem !important;
        }

        .next-line-img {
            display: block !important;
        }

        .about-us-content {
            margin-top: 30px !important;
        }

    }

    @media(max-width:420px) {
        .social-icon-margin {
            margin-left: 2px;
            margin-right: 5px !important;
        }
    }
}