.crypto-page {
  @import "../../shared/common";
  @import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600;700;800&family=Open+Sans:wght@300&family=Poppins:wght@100&family=Roboto:wght@100&display=swap');
  *{
      box-sizing: border-box;
      font-family: 'Mukta', sans-serif;
  }

  /* banner section  */

  .black-screen {
    background-color: black;
    opacity: 50%;
    width: 100%;
    height: 115.375rem;
    position: absolute;
    animation: blackAnimation 3s forwards ease;
    z-index: +1;
  }
  .text-bold{
    font-weight: bold;
  }


  @keyframes blackAnimation {
    0% {
      opacity: 50%;
      visibility: visible;
    }

    50% {
      opacity: 50%;
      visibility: visible;
    }

    75% {
      opacity: 50%;
      visibility: visible;
    }

    100% {
      opacity: 0;
      display: none;
      visibility: hidden;
    }
  }

  .banner-div {
    position: relative;
    height: 55.875rem;
    width: 100%;
  }

  .launching-soon {

    height: 55.875rem;
    width: 100%;
    background-color: $white;
    animation: showAnimation 3s forwards ease;
    z-index: +2;
  }

  .lets-start {
    position: absolute;
    width: 100%;
    height: 100%;
    animation: hideAnimation 1s forwards;

  }

  @keyframes hideAnimation {
    0% {

      display: none;
    }

    100% {

      display: block;
    }
  }

  @keyframes showAnimation {
    0% {
      opacity: 1;
      visibility: visible;
    }

    50% {
      opacity: 1;
      visibility: visible;
    }

    75% {
      opacity: 1;
      visibility: visible;
    }

    100% {
      opacity: 0;
      display: none;
      visibility: hidden;
    }
  }

  .abnes-font {
    display: block;
    font-family: 'abnes', sans-serif;
    color: $black;
  }

  .slider-page {
    height: 100vh;
    width: 100%;
    background: $white;
    font-family: sans-serif;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;


    .info-head {
      margin-left: 5%;
      font-size: 33px;
      color: $purple;

    }

    .info-span {
      color: $orange;
      line-height: 40px;
    }

  }


  .banner-bg {
    background: url($env+"/tax-filling-bg.png");
    background-size: cover;
    background-position: center;
    height: 100%;

    .crptm {
      width: 13.813rem;
      margin: 0;
    }

    .banner-bg-heading {
      font-size: $head;
      margin-top: 20px;
      margin-bottom: 40px !important;
      font-weight: 700;
      color: $purple;
    }

    .banner-main-img {
      color: $purple;
    }

  }



  /* content section  */

  .content-bg {
    min-height: 330px;
    padding-top: 30px;

    .content-head {
      color: $purple;
      font-size: $big-header;
      font-weight: bold;
      text-align: left;

    }
    
    .content-head-para {
      font-size: $big-text;
    }

    .content-1,
    .content-2,
    .content-3 {
      margin-bottom: 10px;

    }


  }


  /* crypto tax   */


  .crypto-bg {
    height: 400px;
    background: $white;
    padding: 0 50px;

    .tax-img {
      display: block;
      width: 820px;
      height: 350px;
    }

    .tax-img-mobile {
      display: none;
    }

  }




  /* feature section  */

  .feature-mobile {
    display: none;
  }


  .feature-bg {
    display: block;
    min-height: 350px;
    margin-top: 30px;
    background: $white;
    border: 0.5px solid $crypto-bg;
    padding: 30px;
    padding-left: 50px;
    padding-right: 50px;

    .feature-bg-img {
      margin: 10px 0px;
    }

    .content-head {
      color: $purple;
      font-size: $big-header;
      font-weight: bold;
      text-align: left;

    }

    .feature-display-flex {
      padding: 0px 10px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      font-size: 18px;
      margin: 0px 12px !important;
      font-weight: 500;
    }

  }

  .feature-1-text {
    color: $purple;
    font-size: $big-text;
  }

  .feature-2-text {
    color: $dark-green;
    font-size: $big-text;
  }

  .feature-3-text {
    color: $orange;
    font-size: $big-text;
  }

  /* tax report section  */

  .tax-report {
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;

    .content-head {
      color: $purple;
      font-size: $big-header;
      font-weight: bold;
      text-align: left;
      margin-top: 20px !important;

    }
  }

  .tax-report-img {
    margin-right: 50px !important;
  }

  .banner1-desktop {
    display: block;
  }


  .banner1-mobile {
    display: none;
  }

  @media (max-width: 991px) {
    .tax-report-img {
      margin-right: 0 !important;
    }
  }

  @media (max-width: 770px) {

    .banner-bg {
      .banner-bg-heading {
        font-size: $head-mobile;
      }
    }

    .content-bg {
      padding: 30px 0;

      .content-head {
        font-size: 20px;
        font-weight: bold;
      }
    }

    .tax-report {
      .content-head {
        font-size: 20px;
        font-weight: bold;
      }
    }

    .feature-mobile {
      display: block;

      .feature-head {
        font-size: 20px;
        color: $purple;
        font-weight: bold;
      }

      .row-div {
        .feature-img {
          width: 158px;
          height: 152px;
        }
      }
    }

    .feature-bg {
      display: none;
    }

    .tax-report-img {
      margin-right: 0 !important;
    }

    .lower-tax-report-img {
      margin-top: 50px;
    }

    .crypto-bg {

      height: auto;
      text-align: center;

      .tax-img {
        display: none;
      }

      .tax-img-mobile {
        display: block;
        margin: auto;
      }
    }


    .abnes-font {
      display: none;
    }

    .banner1-desktop {
      display: none;
    }

    .banner1-mobile {
      display: block;
      width: 100%;

      .launch {
        width: 100%;

        .info-head {
          margin-left: 5%;
          font-size: 33px;
          color: $purple;

        }

        .info-span {
          color: $orange;
          line-height: 40px;
        }
      }
    }
  }

  @media (max-width: 770px) {
    .banner-div {
      height: 48.875rem;
    }
  }

  @media (max-width: 620px) {
    .banner-div {
      height: 43.875rem;
    }
  }

  @media (max-width: 508px) {
    .banner-div {
      height: 40.875rem;
    }
  }

}