@import "../../shared/common";

.faq {
    .heading-div1 {
        font-size: $bigger-head;
        font-weight: bold;
        color: $purple;
    }

    .subheading-div1 {
        font-size: $header;
        font-weight: 600;
    }

    .para-div1 {
        font-size: $big-text;
    }

    .accordion-body {
        padding-top: 0px;
    }

    .accordion-button {
        color: $purple;
        background-color: $portfolio-bg;
        box-shadow: none;
        border: 1px solid $border-grey;
        font-size: $header;
        font-weight: bold;
        margin-bottom: 20px;

        &::after {
            background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='%23333' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' d='M8 0a1 1 0 0 1 1 1v6h6a1 1 0 1 1 0 2H9v6a1 1 0 1 1-2 0V9H1a1 1 0 0 1 0-2h6V1a1 1 0 0 1 1-1z' clip-rule='evenodd'/%3e%3c/svg%3e");
        }
    }

    .accordion-button:focus {
        border-color: $border-grey;
        box-shadow: none;
    }

    .accordion-button:not(.collapsed) {
        color: $purple;
        background-color: $portfolio-bg;
        box-shadow: none;

        &::after {
            background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='%23333' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' d='M0 8a1 1 0 0 1 1-1h14a1 1 0 1 1 0 2H1a1 1 0 0 1-1-1z' clip-rule='evenodd'/%3e%3c/svg%3e");
        }
    }

    .accordion-item {
        border: none;
    }

    .para-ques {
        font-size: $big-text;
        font-weight: bold;
    }

    .para-ans {
        font-size: $big-text;
    }

    .link {
        color: $purple;
    }

    .left-images {
        float: left;
        margin-right: 20px;
    }

    dl {
        margin-left: 1rem;
    }

    dd {
        display: list-item;
        list-style-type: square;
        padding-inline-start: 8px;

        &::marker {
            color: $orange;
            font-size: $subheader;
        }

        dd {
            display: list-item;
            list-style-type: disc;
            margin-left: 1rem;

            &::marker {
                font-size: $big-text;
            }
        }
    }

    ul {
        list-style: none;
        padding: 0;
    }

    @media (min-width: 0) and (max-width: 767px) {
        .heading-div1 {
            font-size: $big-header;
            font-weight: 600;
        }

        .subheading-div1 {
            font-size: $subheader;
        }

        .para-div1 {
            font-size: $normal-text;
        }

        .accordion-button {
            font-size: $subheader;
        }

        .para-ques {
            font-size: $normal-text;
        }

        .para-ans {
            font-size: $normal-text;
        }
    }
}