@import "../shared/_common.scss";

.notify-form {
  height: 250px;

  .get-Notified-form {
    margin: auto;
    width: 40.625rem;
    height: 10.5rem;
    margin-top: 10px;
    display: block;
  }

  .email-notified {
    border-radius: 5px;
    padding-left: 20px;
    border: 1px solid $border;
    width: 37.563rem;
    height: 56px;
    &::placeholder{
      font-size: $big-text;
    }
  }

  .lower-form {
    margin: 0;
    padding: 0;
    padding-right: 40px;
  }

  .captcha-notified {
    width: 15rem;
    height: 56px;
    padding: 0;
    padding-left: 60px;
  }

  .button-get-notified {
    &:hover {
      background-color: $orange;
      color: $white;
    }

    &:disabled {
      background-color: #D0D0D0;
      color: #676767;
    }

    display: block;
    padding: 0;
    margin-left: 9.2rem;
    border-radius: 5px;
    background-color: $orange;
    border: none;
    text-align: center;
    display: inline-block;
    width: 10.425rem;
    height: 56px;
    text-decoration: none;
    color: $white;
    font-size: $big-text;
    font-weight: 600;
    margin-top: 7.5px;
  }

  .button-get-notified-mobile {
    display: none;
  }

  .loader-div {
    text-align: end;
    padding-right: 70px;
    position: relative;
  }

  .loader-get-notified {
    position: absolute;
    bottom: 12px;
    right: 70px;
  }

  // ERROR DIV STYLING

  .error-div-notified {
    position: relative;
    background-color: $white;
    width: 45.625rem;
    margin: auto;
    height: 11.5rem;
    border: 1px solid #707070;
    border-radius: 10px;
    margin-top: 5px;
    bottom: 30px;

    .dismiss-error {
      position: absolute;
      top: 0px;
      right: 0px;
      margin-top: 10px;
      margin-right: 10px;
      border: none;
      height: 33px;
      background: none;
      width: 33px;
      padding: 0;
    }

    .silly-bot {
      margin: 0;
      margin-left: 7.563rem;
      padding: 0;
      padding-bottom: 8px;
      height: 11.438rem;
      width: 10.313rem;
    }

    .error-right-notified {
      height: 6.375rem;
      text-align: center;
      width: 25rem;
      margin-top: 46px;
      margin-bottom: 34px;
      margin-left: 23px;
      padding-right: 52px;
      padding-left: 0;
    }

    .swr {
      font-size: 28px;
    }

    .retry {

      .retry-img {
        margin-right: 5px;
        margin-bottom: 3px;
      }

      &:hover {
        background-color: $orange;
        color: $white;
      }

      padding: 7px 0px;
      border-radius: 23px;
      background-color: #6660AB;
      border: none;
      text-align: center;
      display: inline-block;
      width: 8.813rem;
      height: 46px;
      text-decoration: none;
      color: white;
      font-size: 19.21px;
    }
  }

  .sub-title {
    color: #676767;
    text-align: center;
    margin: auto;

    padding: 0;
    font-weight: 500;
    font-size: 18px;

  }





  // THANK YOU DIV STYLING

  .thanksbox-notified {
    position: relative;
    background-color: $white;

    .Thanks-box-content {
      position: absolute;
      bottom: 20px;
      width: 100%;

      .thanks-p {
        margin-bottom: 0;
      }
    }

    font-size: 27px;
    width: 45.625rem;
    height: 11.5rem;
    border-radius: 10px;
    border: 1px solid #CFCFCF;
    margin: auto;
    position: relative;
    bottom: 30px;

    .dismiss-thanks-cross {
      margin: 0;
    }

    .dismiss-thanks-button {
      position: absolute;
      top: 0px;
      right: 0px;
      margin: 0;
      border: none;
      background: none;
    }

    .thanks-tick {
      margin: 0;
      margin-top: 12px;
      margin-bottom: 10px;
    }
  }

  @media (max-width: 770px) {

    height: 303px;


    .get-Notified-form {
      padding: 0;
      margin: auto;
      max-width: 416px;

      .sub-title {
        color: #676767;
        width: 100%;
        text-align: center;
        margin: auto;
        margin-top: 43px;
        padding: 0;
        font-weight: 500;
        font-size: 18px;
      }

      .email-notified {
        max-width: 416px;
        margin: auto;
      }

      .lower-form {
        width: 100%;
        padding: 0;
        padding-left: 30px;

        .captcha-notified {
          padding-left: 0;
          height: 100%;
          width: auto;
        }

        .button-get-notified {
          display: none;
        }

        .loader-div {
          text-align: end;
          padding-right: 15px;
          position: relative;
        }

        .loader-get-notified {
          right: 15px;
        }

        .button-get-notified-mobile {
          &:hover {
            background-color: $orange;
            color: $white;
          }

          &:disabled {
            background-color: #D0D0D0;
            color: #676767;
          }

          display: block;
          border: none;
          width: 47px;
          height: 47px;
          border-radius: 50%;
          background-color: $purple;
          margin-left: 35px;
          margin-top: 13px;
        }
      }
    }

    .error-div-notified {
      width: 350px;
      height: 236px;
      margin: auto;
      margin-top: 66px;
      border-radius: 10px;
      position: relative;

      .silly-bot {
        position: absolute;
        left: 0;
        margin: 0;
        padding: 0;
        width: 181.01px;
        height: 202px;
      }

      .error-right-notified {
        margin: 0;
        padding: 0;
        position: absolute;
        right: 43px;
        top: 81px;
        width: 109px;

        .swr {
          font-size: 18px;
          font-weight: 550;
          color: #676767;
          margin-bottom: 5px;
        }

        .retry {
          width: 83px;
          height: 25px;
          border-radius: 30px;
          font-size: 13px;
          padding: 0;
        }
      }
    }

    .thanksbox-notified {
      width: 350px;
      height: 236px;
      border-radius: 10px;

      .thanks-tick {
        margin-top: 26px;
      }

      .Thanks-box-content {
        width: 308px;
        height: 102px;
        font-size: 25px;
        left: 20px;
        bottom: 32px;
      }
    }
  }

  @media (max-width: 460px) {
    .get-Notified-form {
      max-width: 100%;
      margin-bottom: 0;

      .email-notified {
        max-width: 100%;
      }

      .lower-form {
        .button-get-notified-mobile {
          margin-left: 13px;
        }
      }


    }
  }

  @media (max-width: 400px) {
    .get-Notified-form {
      .lower-form {
        padding-left: 7px;

        .button-get-notified-mobile {
          margin-left: 0;
        }
      }
    }
  }
}