@import "../../../sass/shared/variable.scss";

.user-password {
  @media(min-width:768px) {
    * {
      box-sizing: border-box;
    }

    .container-bg-user {
      background: url($map);
      background-position: center;
      background-size: cover;
      
      padding-top: 2rem !important;
      padding-bottom: 2rem !important;

    }
    .form-container-user {
      background: $white;
      border-radius: 10px;
      box-shadow: $box-shadow;
      width: 650px;
      margin: auto;

    }
    .jumbotron {
      height: 150px;
      padding: 20px 0px;
      border-radius: 12px 12px 0px 0px;
      width: 100%;

      background: #f7f7f7;

      h1 {
        font-size: 26px;
        color: $purple;
        font-weight: 500;
      }

      .button-hello {
        padding: 0px 150px !important;
        border: 0.5px solid $grey  !important;
        font-weight: 400 !important;
        color: $grey  !important;
        font-size: 22px !important;
      }
    }

    .bg-left-side {
      width: 100%;
      background: #f6f6f6;

      height: 330px;
      color: white;

    }
    .bg-white-col {
      width: 100%;
      background: #f6f6f6;
      height: 120px;

      span {
        padding: 0px 20px;
        text-align: center;
      }

      
    }
    .active-heading1 {
      width: 100% !important;
      margin: 0;
      padding: 0;
      background-color: $purple;
      color: $white;
      padding: 8px 0px;

      cursor: pointer;

      a {
        color: white;
        text-decoration: none;
      }
    }
    
    .active-heading {
      width: 100%;
      text-align: center;
      color: $grey;
      cursor: pointer;
      border-bottom: 0.5px solid $grey;
      padding: 8px 0px;

      a {
        color: $black;
        text-decoration: none;
      }
    }
    .active-btn {
      width: 100%;
      border: none;
      color: $white;
      padding: 8px 0px;
      font-size: 17px;
      font-weight: 500;
      border-left: 4px solid $orange;
      text-align: left;
      background: $purple;
      cursor: default;
      padding-left: 5px;
    }

    .left-btns {
      width: 100%;
      border: none;
      color: #333333;
      padding: 8px 0px;
      font-size: 17px;
      border-left: 4px solid #cfcddc;
      bottom: bottom 1px ;
      text-align: left;
      padding-left: 5px;

      &:hover {
        text-decoration: underline;
      }
    }

    .icon-width-fixing {
      width: 30px;
      height: auto;
      text-align: center;
      display: inline-block;
    }

    .top-banner {
      background: #f7f7f7;
      width: 100%;
      padding: 20px 0px;
      border-radius: 10px 10px 0px 0px;
      position: relative;

      .cross-btn {
        position: absolute;
        right: -10px;
        top: -10px;
        cursor: pointer;
      }

      .settings-h1 {
        font-size: 32px;
        color: $purple;
        font-weight: 500;
      }

      .hello-user {
        margin-bottom: 0px;
        width: 380px;
        margin: auto;
        border: 1px solid $grey;
        font-weight: 500;
        color: #273251;
        font-size: 24px;
        background: #f3f3f3;
        overflow: hidden;
        border-radius: 10px;
      }
    }

    .container-bg-user {
      background: url($map);
      background-position: center;
      background-size: cover;
      padding-top: 2rem !important;
      padding-bottom: 2rem !important;
      height: auto;

    }
    .form-container-user {
      background: $white;
      border-radius: 10px;
      box-shadow: $box-shadow;
      width: 650px;
      margin: auto;

      .jumbotron {
        height: 150px;
        padding: 20px 0px;

        width: 100%;
        border-radius: 12px 12px 0px 0px;
        background: #f7f7f7;

        h1 {
          font-size: 26px;
          color: $purple;
          font-weight: 500;
        }

        .button-hello {
          padding: 0px 150px !important;
          border: 1px solid $grey  !important;
          font-weight: 400 !important;
          color: $grey  !important;
          font-size: 22px !important;
        }
      }

      .bg-left-side {
        width: 100%;
        background: #f6f6f6;

        height: 330px;
        color: white;

        .bg-white-col {
          width: 100%;
          background: #f6f6f6;
          height: 120px;

          .active-heading1 {
            width: 100% !important;
            margin: 0;
            padding: 0;
            background-color: $purple;
            color: $white;
            padding: 8px 0px;

            cursor: pointer;

            a {
              color: white;
              text-decoration: none;
            }
          }

          .active-heading {
            width: 100%;
            text-align: center;
            color: $grey;
            cursor: pointer;
            border-bottom: 0.5px solid $grey;
            padding: 8px 0px;

            a {
              color: $black;
              text-decoration: none;
            }
          }
        }
      }

    }
    .user-form {
      padding-top: 0;
      padding-bottom: 0;
      padding-right: 2rem;

    }
    .form-container-group {
      width: 50% !important;
      display: flex !important;
      display: inline-block !important;

      input {
        outline: 0;
        border: 0.7px solid rgb(170, 169, 169);
        width: 97%;
        margin-right: 5px;
        padding: 2px;
      }
    }

    .text-heading {
      color: $purple;
      margin-bottom: 10px;
      font-size: 24px;
    }

    .form-group input {
      border-radius: 8px;
      padding: 5px !important;
      margin-bottom: 7px;
    }

    .right-align-email {
      display: flex;
      align-items: center;
      justify-content: space-between;

      span a {
        font-size: 12px;
        color: rgb(56, 172, 211);
        text-decoration: underline;
      }
    }

    .button-submit {
      background-color: $green;
      margin-top: 15px;
      margin-right: 10px;
      font-weight: 500;
      border: none;
      border-radius: 10px;
      padding: 5px 10px;
      text-decoration: none;
      text-decoration: none;
    }

    .bg-change {
      color: black;
      background: rgb(218, 214, 214);
    }

    .form-input-label {
      font-size: $big-text;
      color: $purple;
    }

    .password-eye-icon {
      position: absolute;
      top: 33px;
      right: 10px;
      color: $grey;
    }

    .success {
      background: $success-msg-green;
      position: relative;
      color: $success-msg-green-border;
      width: 100%;
      border-radius: 10px;
      border: 1px solid $success-msg-green-border;
      padding: 7px 14px;

      .success-cross {
        position: absolute;
        top: -5px;
        right: -5px;
        cursor: pointer;
      }

      .success-para {
        word-wrap: break-word;
        margin: 0px;
      }
    }

    .error {
      background: $error-msg-red;
      color: $error-msg-red-text;
      width: 100%;
      border-radius: 10px;
      border: 1px solid $error-msg-red-border;
      position: relative;
      padding: 7px 14px;

      .error-para {
        word-wrap: break-word;
      }

      .error-cross {
        position: absolute;
        top: -6px;
        right: -6px;
        cursor: pointer;
      }
    }

    .confirm-pwd-info {
      background-color: $signup-info-box;
      border: 1px solid $signup-info-box-border;
      border-radius: 5px;
      font-size: 16px;
      height: 36px;
      padding: 4px 44px;
      margin-top: 14px;
      position: relative;

      .up-arrow {
        position: absolute;
        bottom: 34px;
        left: 10px;
      }

      .warn-input {
        position: absolute;
        left: 14px;
        top: 7px;
      }

      .dismiss-warning {
        position: absolute;
        right: -10px;
        top: -15px;
      }
    }

    .pwd-info {
      background-color: $signup-info-box;
      border: 1px solid $signup-info-box-border;
      border-radius: 10px;
      padding: 1rem;
      margin-bottom: 10px;

      .text-green {
        color: #489332;
      }

      .text-red {
        color: $failure;
      }

      .pwd-info-heading {
        font-size: 17px;
        font-weight: 600;
      }

      .the-info {
        margin: 0;
        padding: 0;
        list-style: none;

        .icon {
          position: relative;
          top: -2px;
        }

        .info-tick {
          margin-left: 9px;
          margin-right: 6px;
        }
      }
    }

    .error2.btn {
      position: absolute;
      top: -10px;
      right: -6px;
    }
  }

  @media(max-width:767.5px) {
    .settings-h1 {
      margin-top: 5rem;
      font-size: 26px;
      color: $purple;
      font-weight: 500;
      .settings-icon{
        width: 25px;
      }
    }
    .hello-user {
      margin-bottom: 0px;
      max-width: 380px;
      margin: auto;
      border: 1px solid $grey;
      font-weight: 500;
      color: #273251;
      font-size: 27px;
      background: #f3f3f3;
      overflow: hidden;
      border-radius: 10px;

      @media(max-width:390px) {
        margin-left: 10px;
        margin-right: 10px;
      }
    }

    .active-btn {
      width: 100%;
      border: none;
      color: $white;
      padding: 8px 0px;
      font-size: 17px;
      font-weight: 500;
      text-align: left;
      background: $purple;
      cursor: default;
      padding-left: 5px;
    }

    .left-btns {
      width: 100%;
      border: none;
      color: #333333;
      padding: 8px 0px;
      font-size: 17px;
      border-bottom: 1px solid #939393;
      text-align: left;
      padding-left: 5px;

      &:hover {
        text-decoration: underline;
      }
    }

    .btn-arrow-1 {
      margin-left: calc(100% - 10.8rem);
    }

    .btn-arrow-2 {
      margin-left: calc(100% - 12rem);
    }

    .btn-arrow-3 {
      margin-left: calc(100% - 13.3rem);
    }
    .btn-arrow-4 {
      margin-left: calc(100% - 10.6rem);
    }

    .pass-container {
      padding-top: 1rem;
      padding-left: 2rem;
      padding-right: 2rem;
      .text-heading {
        color: $purple;
        margin-bottom: 10px;
        font-size: 20px;
      }
    }
    .success {
      background: $success-msg-green;
      position: relative;
      color: $success-msg-green-border;
      width: 100%;
      border-radius: 10px;
      border: 1px solid $success-msg-green-border;
      padding: 7px 14px;

      .success-cross {
        position: absolute;
        top: -5px;
        right: -5px;
        cursor: pointer;
      }

      .success-para {
        word-wrap: break-word;
        margin: 0px;
      }
    }
    .error {
      background: $error-msg-red;
      color: $error-msg-red-text;
      width: 100%;
      border-radius: 10px;
      border: 1px solid $error-msg-red-border;
      position: relative;
      padding: 7px 14px;

      .error-para {
        word-wrap: break-word;
      }

      .error-cross {
        position: absolute;
        top: -6px;
        right: -6px;
        cursor: pointer;
      }
    }
    .form-group input {
      border-radius: 8px;
      padding: 5px !important;
      margin-bottom: 7px;
    }
    .form-input-label {
      font-size: $normal-text;
      color: $purple;
      margin: 1px 0px 4px 0px;
    }

    .password-eye-icon {
      position: absolute;
      top: 33px;
      right: 10px;
      color: $grey;
    }
    .button-submit {
      background-color: $green;
      margin-top: 15px;
      margin-right: 10px;
      font-weight: 500;
      border: none;
      border-radius: 10px;
      padding: 5px 10px;
      text-decoration: none;
      text-decoration: none;
    }
    .pwd-info {
      background-color: $signup-info-box;
      border: 1px solid $signup-info-box-border;
      border-radius: 10px;
      padding: 1rem;
      margin-bottom: 10px;

      .text-green {
        color: #489332;
      }

      .text-red {
        color: $failure;
      }

      .pwd-info-heading {
        font-size: 17px;
        font-weight: 600;
      }

      .the-info {
        margin: 0;
        padding: 0;
        list-style: none;

        .icon {
          position: relative;
          top: -2px;
        }

        .info-tick {
          margin-left: 9px;
          margin-right: 6px;
        }
      }
    }
    .error2.btn {
      position: absolute;
      top: -10px;
      right: -6px;
    }
    .confirm-pwd-info {
      background-color: $signup-info-box;
      border: 1px solid $signup-info-box-border;
      border-radius: 5px;
      font-size: 16px;
      height: 36px;
      padding: 4px 44px;
      margin-top: 14px;
      position: relative;

      .up-arrow {
        position: absolute;
        bottom: 34px;
        left: 10px;
      }

      .warn-input {
        position: absolute;
        left: 14px;
        top: 7px;
      }

      .dismiss-warning {
        position: absolute;
        right: -10px;
        top: -15px;
      }
    }
  }
}