@import '../shared/_variable.scss';

.contact-us {

    // DESKTOP VIEW

    .mobile-form {
        display: none;
    }

    .desktop-form {
        display: block;
    }

    .select-contact {
        display: none;
    }

    .main-header,
    .loader {
        text-align: center;
    }

    .tick-img-contact {
        margin-top: 25px;
    }

    .contact-us-header {
        text-align: center;
        color: $white;
        font-size: 40px;
    }

    .main-header {
        background-color: $purple;
        height: 10.438rem;
        padding-top: 20px;
    }

    .main-form {
        margin: auto;
        border: 2px solid $border;
        box-shadow: 0em 0em 0.06em 0.06em #ebebeb;
        border-radius: 13px;
        background-color: #FCFEFF;
        width: 73.25rem;
        min-height: 47.875rem;
        margin-top: 50px;
        margin-bottom: 50px !important;
    }

    .get-in-touch {
        text-align: center;
        color: #6961AB;
        margin-top: 30px;
        font-size: 31px;
        font-weight: 600;
    }

    .form-left-contact {
        padding: 0;
        height: 430px;
        width: 450px;
    }

    .input-contact {
        margin-bottom: 5px !important;
        font-size: 20px;

        .input-labels {
            margin-bottom: 6px;
        }
    }

    .captcha-contact {
        margin-top: 20px;
    }

    .form-right {
        padding-left: 150px;
    }

    .warn {
        font-size: 15px;
        color: #A7A7A7;
    }

    .form-left-contact {
        .select-label {
            font-size: 20px;
            color: #2C2C2C;
            margin-left: 103px;
            margin-bottom: 6px;
            padding: 0;
            height: 27px;
        }
    }

    .lets-select {
        border: 1px solid $border;
        border-radius: 10px;
        min-height: 25rem;
        width: 27.938rem;
        margin-left: 6.438rem;
        margin-right: 0;
    }

    .select-items {
        font-size: 15px;
        color: #2C2C2C;
        margin: 12px 34px;
    }

    .form-check {
        padding: 10px;
        border-bottom: 1px solid #DEDEDE;
    }

    .compulsary {
        color: $danger;
    }

    .form-right {
        .input-box {
            border-radius: 10px;
            border: 1px solid $border;
            width: 27.938rem;
            height: 45px;
        }
    }

    .message-box {
        border-radius: 10px;
        border: 1px solid $border;
        width: 27.938rem;
        height: 8.438rem;
    }

    .button-contact {
        &:hover {
            background-color: $orange;
            color: $white;
        }

        &:disabled {
            background-color: #D0D0D0;
            color: #676767;
        }

        margin: 20px auto;
        padding: 7px 0px;
        border-radius: 5px;
        background-color: $orange;
        border: 1px solid $dark-green;
        text-align: center;
        display: inline-block;
        width: 11.438rem;
        height: 45px;
        text-decoration: none;
        color: $white;
        font-size: $subheader;
    }

    .thank-you-contact {
        width: 31.375rem;
        min-height: 29.75rem;
        border: 1px solid #CCCCCC;
        margin: auto;
        margin-top: 133px;
        margin-bottom: 50px;
        border-radius: 10px;
        box-shadow: 3px;
    }

    .thanks {
        font-size: 31.96px;
        color: #6560AA;
        margin-top: 42px;
        margin-bottom: 14px;
    }

    .message-sent {
        font-size: 23px;
        color: $dark-blue;
        margin: 0;
    }

    .button-back-contact {
        margin: 20px auto;
        padding: 7px 0px;
        border-radius: 10px;
        background-color: $light-green;
        border: 1px solid $dark-green;
        text-align: center;
        display: inline-block;
        width: 24.813rem;
        height: 42px;
        text-decoration: none;
        color: $dark-blue;
        font-size: 19.21px;
        margin-bottom: 44px;
    }

    .error-div-contact {
        width: 31.25rem;
        margin: auto;
        height: 272px;
        border: 1px solid #707070;
        border-radius: 10px;
        margin-top: 133px;
        margin-bottom: 50px;
    }

    .silly-bot-contact {
        margin: 0;
        padding: 0;
        height: 16.875rem;
        width: 12.5rem;
    }

    .error-right-contact {
        height: 6.375rem;
        text-align: center;
        width: 13.313rem;
        margin-top: 6.625rem;
        margin-left: 50px;
    }

    .swr {
        font-size: 17px;
    }

    .retry {
        &:hover {
            background-color: $orange;
            color: $white;
        }

        padding: 7px 0px;
        border-radius: 23px;
        background-color: $purple;
        border: none;
        text-align: center;
        display: inline-block;
        width: 8.813rem;
        height: 46px;
        text-decoration: none;
        color: $white;
        font-size: 19.21px;
    }

    // RESPONSIVE VIEWS

    @media (max-width: 1190px) {
        .mobile-form {
            display: none;
        }

        .main-form {
            width: 60rem;

            .select-contact {
                display: none;
            }

            .form-right {
                padding-left: 88px;
                padding-right: 0;
            }

            .select-label {
                margin-left: 3.438rem;
            }

            .lets-select {
                border: 1px solid #BABABA;
                border-radius: 10px;
                min-height: 25rem;
                width: 24.938rem;
                margin-left: 3.438rem;
                margin-right: 0;
            }

            .input-box,
            .message-box {
                width: 18.25rem;
            }
        }
    }

    @media (max-width: 991px) {
        .mobile-form {
            display: none;
        }

        .main-form {
            width: 45rem;

            .select-contact {
                display: none;
            }

            .form-left-contact {

                width: 17.938rem;

                .select-label {
                    margin-left: 1.9rem;
                }

                .lets-select {
                    border: 1px solid #BABABA;
                    border-radius: 10px;
                    min-height: 25rem;
                    width: 17.938rem;
                    margin-left: 1.9rem;
                    margin-right: 0;
                }
            }

            .form-right {
                padding-left: 72px;
                padding-right: 0;
            }
        }
    }

    @media (max-width: 770px) {
        .desktop-form {
            display: none;
        }

        .thank-you-contact {
            width: 21.375rem;
            min-height: 29.75rem;
            border: 1px solid #CCCCCC;
            margin: auto;
            margin-top: 8.313rem;
            margin-bottom: 50px;
            border-radius: 10px;

            .thanks {
                font-size: 23.96px;
                color: #6560AA;
                margin-top: 49px;
                margin-bottom: 14px;
            }

            .message-sent {
                font-size: 16px;
            }

            .button-back-contact {
                width: 19.813rem;
            }
        }

        .error-div-contact {
            width: 22.25rem;
            margin: auto;
            height: 178px;
            border: 1px solid #707070;
            border-radius: 10px;
            margin-top: 8.313rem;
            margin-bottom: 50px;

            .silly-bot-contact {
                margin: 0;
                padding: 0;
                height: 10.875rem;
                width: 9.5rem;
            }

            .error-right-contact {
                height: 4.375rem;
                text-align: center;
                width: 10.313rem;
                margin-top: 3.625rem;
                margin-left: 14px;

                .swr {
                    font-size: 12px;
                    margin-bottom: 0.5rem;
                }

                .retry {
                    &:hover {
                        background-color: $orange;
                        color: $white;
                    }

                    padding: 6px 0px;
                    border-radius: 23px;
                    background-color: #6660AB;
                    border: none;
                    text-align: center;
                    display: inline-block;
                    width: 6.813rem;
                    height: 37px;
                    text-decoration: none;
                    color: #FFFFFF;
                    font-size: 16.21px;
                }
            }
        }

        .mobile-form {
            display: block;

            .main-form {
                width: 22.063rem;
                min-height: 39.375rem;
                position: relative;
                border-radius: 0;

                .select-contact {
                    display: block;
                    opacity: 1;
                    visibility: visible;
                }

                .get-in-touch {
                    text-align: start;
                    height: 26px;
                    font-size: 19px;
                    padding: 0;
                    margin-top: 18px;
                }

                .form-left-contact {
                    display: none;
                }

                .form-right {
                    padding: 0;
                    position: absolute;
                    top: 58px;
                    width: 20.063rem;

                    .input-box,
                    .select-box {
                        font-size: 12px;
                        width: 19rem;
                        height: 30px;
                        border-radius: 10px;
                    }

                    .message-box {
                        font-size: 12px;
                        width: 19rem;
                        min-height: 89px;
                    }

                    .input-labels {
                        font-size: 14px;

                        .warn {
                            font-size: 12px;
                        }
                    }
                }

                .button-contact {
                    &:hover {
                        background-color: $orange;
                        color: $white;
                    }

                    &:disabled {
                        background-color: #D0D0D0;
                        color: #676767;
                    }

                    position: absolute;
                    margin: 0;
                    width: 7.563rem;
                    height: 29px;
                    border-radius: 5px;
                    font-size: 12px;
                    top: 35.313rem;
                    background-color: $green;
                }

                .loader {
                    position: absolute;
                    top: 35.313rem;
                    margin: 0;
                    text-align: start;
                }
            }
        }
    }
}