@import "common";

.header-navbar {
    .desktop-view {

        .navbar-container {
            display: block !important;
        }

        .sticky {
            margin-bottom: 57px;
        }

        .bg-header {
            background: $purple;
            color: $white;
        }

        .header-logo {
            width: 240px;
            height: 40px !important;
            padding: 0.5rem 0rem 0rem 0rem;
        }

        .btn-signup {
            background-color: $purple;
            border: 0;
            color: $white;
            font-size: $big-text;
            font-weight: 600;
            padding: 3px 15px;
            border-radius: 25px;

            &:hover {
                background-color: $orange;
            }
        }

        .text-signup {
            color: $white;
            font-weight: bold;
            text-decoration: none;
            background: $orange;
            height: 58px;
            outline: none;
            border: none;
            padding: 0px 12px;
        }

        .header-login {
            color: $white !important;
            font-size: $big-text;
            font-weight: 600;
            text-decoration: none;

            &:hover {
                color: $orange !important;
            }
        }

        .navbar-nav {
            padding-top: 6px;
            padding-bottom: 6px;
        }

        .navbar-item {
            margin: 0rem 3.5rem;
            cursor: pointer;

            &:hover {              
                    border-top: 6px solid $orange;
                    margin-top: -6px;
            }

            &.active {
                border-top: 6px solid $orange;
                margin-top: -6px;
            }

            .header-text {
                font-weight: 600 !important;
                font-size: $big-text !important;
                color: $white !important;

                &.active {
                    font-weight: bold !important;
                    border-top: 6px solid $orange;
                    margin-top: -6px;
                }
            }

            .dropdown-text {
                font-weight: 600 !important;
                font-size: $normal-text !important;
                color: $banner-para !important;

                &.active {
                    font-weight: bold !important;
                    background-color: $white;
                }
            }

        }

        .dropdown {
            .dropdown-menu {
                &.hide {
                    display: none;
                }

                &.show {
                    display: block;
                    margin-top: 0.125rem;
                }
            }


            &:hover {
                .dropdown-menu {
                    display: block;
                    margin-top: 0.125rem;

                }
            }

        }

        .user-btn {
            font-weight: 600 !important;
            font-size: $big-text !important;
            color: $white !important;

            &:hover {
                font-weight: 600 !important;
            }
        }

        .user-dropdown {
            font-weight: 600 !important;
            font-size: $normal-text !important;
            color: $banner-para !important;
        }
    }

    .mobile-view {
        display: none;

        .navbar-nav .dropdown-menu {
            background: $purple;
            color: $white;
            border: 1px solid $white;
        }

        .new-bg {
            background: $purple;
            position: relative;
            margin: auto;
            width: 208px;
            margin-top: -20px;

            .no-show {
                visibility: hidden;
            }
        }

        hr {
            background: $white;
            color: $white;
        }

        .bg-header {
            background: $purple;
            color: $white;
        }

        .header-logo {
            height: 30px;
            width: 182px;
        }

        .navbar-toggler {
            width: 20px;
            height: 20px;
            position: relative;
            display: -webkit-flex;
            display: flex;
            transition: .5s ease-in-out;
            margin-top: 0.4rem;
        }

        .navbar-toggler,
        .navbar-toggler:focus,
        .navbar-toggler:active,
        .navbar-toggler-icon:focus {
            outline: none;
            box-shadow: none;
            border: 0;
        }

        .navbar-toggler span {
            margin: 0;
            padding: 0;
        }

        .toggler-icon {
            display: block;
            position: absolute;
            height: 3px;
            width: 100%;
            background: $purple;
            border-radius: 1px;
            opacity: 1;
            left: 0;
            transform: rotate(0deg);
            transition: .25s ease-in-out;
            color: $white !important;
        }

        .middle-bar {
            margin-top: 0px;
        }

        /* when navigation is clicked */

        .navbar-toggler .top-bar {
            margin-top: 0px;
            transform: rotate(135deg);
            background: $white;
        }

        .navbar-toggler .middle-bar {
            opacity: 0;
            filter: alpha(opacity=0);
        }

        .navbar-toggler .bottom-bar {
            margin-top: 0px;
            transform: rotate(-135deg);
            background: $white;
        }

        /* State when the navbar is collapsed */

        .navbar-toggler.collapsed .top-bar {
            margin-top: -10px;
            transform: rotate(0deg);
        }

        .navbar-toggler.collapsed .middle-bar {
            opacity: 1;
            filter: alpha(opacity=100);
        }

        .navbar-toggler.collapsed .bottom-bar {
            margin-top: 10px;
            transform: rotate(0deg);
        }

        /* Color of 3 lines */

        .navbar-toggler.collapsed .toggler-icon {
            background: $white;
        }

        .w-nav-overlay {
            height: 10000px;
        }

        .sticky {
            margin-bottom: 58.05px;
        }



        .dropdown-text {
            font-weight: 600 !important;
            font-size: $header !important;
            color: $white !important;

            &.active {
                font-weight: bold !important;
                background-color: $white;
                color: $black !important;
            }
        }

        .btn-login {
            border: 1px solid $purple;
            background: $white;
            color: $purple;
            font-size: $header;
        }

        .btn-start {
            border: none;
            background: $orange;
            color: $white;
            font-size: $header;
            height: 40px;
        }

        .dropdown-item:focus,
        .dropdown-item:hover {
            background-color: $white;
            color: $black !important;
        }
    }

    @media(max-width:1200px) {
        .desktop-view {
            .navbar-item {
                margin: 0rem 2.5rem;
                cursor: pointer;

                &.active {

                    .header-text {
                        font-weight: bold !important;
                    }
                }

                .header-text {
                    font-weight: 500 !important;
                    font-size: $header !important;
                    color: $white !important;
                }

                &:hover {
                    .navbar-link {
                        font-weight: bold !important;
                    }
                }

                &.active {
                    border-top: 6px solid $orange;
                    margin-top: -6px;


                    .header-text {
                        font-weight: bold !important;
                    }
                }

                .header-text {
                    font-weight: 600 !important;
                    font-size: $big-text !important;
                    color: $white !important;
                }

                .dropdown-text {
                    font-weight: 600 !important;
                    font-size: $normal-text !important;
                    color: $banner-para !important;

                    &.active {
                        font-weight: bold !important;
                        background-color: $white;
                    }
                }
            }
        }
    }


    @media(max-width:991.5px) {
