@import '../../shared/common';

.transaction-activity-module{
    .user-portfolio-heading{
    font-size: $header;
    color: $purple;
    font-weight: 600;
    }
.add-exchange-btn {
    padding: 5px 18px;
    border-radius: 3rem;
    font-size: $big-text;
    color: $white;
    background-color: $purple;
    &:hover{
        color: $white; 
    }
  }
  .details-box {
    background-color: $white;
    border-radius: 10px;
    border: 1px solid $border;
    padding: 1rem;
    
  }

  .section-heading {
    font-weight: 500;
    color: $purple;
    font-size: $big-text;
    margin: 0px;
    margin-bottom: 0.5rem;
    line-height: 2;
  }
  
  .details-box 
  
  .hr-line {
    color: $border;
  }
  
  .detail-text{
    margin-top: 0.7rem;
    .portfolio-item {
        font-size: 1rem;
        font-weight: 700;
        line-height: 2;
        color: #222222;
      }
  } 
  
  .exchange-crypto-img {
    width: 42px;
    height: auto;
  }
  
  .btn-div{
    text-align: center;
    .btn-app-primary.add-more-btn {
      padding: 5px 18px;
      border-radius: 12px;
      font-size: $normal-text;
      color: $white;
      background-color: $purple;
      border: 1px solid $border;
      margin-top:0.85rem;
    }
  }
  .user-portfolio-info-icon{
    width: 1rem;
    height: 1rem;
    position: relative;
    top: 0px;
    fill: $tooltip-icon;
    svg{
      fill: $tooltip-icon;
      position: relative;
      top: 0px;
    }
  }
  .exchange-indicator{
    position: absolute;
    top: 0;
    right: 0;
    height: 0.8rem;
    width: 0.8rem;
    border-radius: 50%;
  
  }

  .section-box {
    background-color: $white;
    border: 1px solid $border;
    border-radius: 10px;
    padding: 1rem;
    
  }
 

  .exchange-loaders{
    img{
      width: 3rem;
      height: auto;
    }
  } 
  .rotated-btn {
    transform: rotate(-180deg);
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .non-rotated-btn {
    transform: rotate(0deg);
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .info-table-border {
    border: 1px solid $border;
    border-radius: 7px;
    max-height: 18rem;
    overflow: auto;
  }

  .static-border-bottom {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    background-color: $purple;
    color:$white;
  }
  
  th.border-adjustment{
    border-color:$white !important;
    padding: 0.5rem !important;
    font-size: $text;
    font-weight: 500 !important;
  }
  
  .info-table-border table tbody tr td{
    padding: 0.5rem !important;
    font-size: $text !important;
  }
  
  .five-transaction {
    max-height: 15rem;
    overflow: hidden;
    overflow-x: auto;
    .info-table-border{
      overflow: hidden;
      overflow-x: auto;
    }
  }
  
  .ten-or-more-transaction {
    overflow: auto;
    .info-table-border{
      overflow: auto;
    }
  }

  .big-text-detail {
    font-size: 1.5rem;
    font-weight: 900;
  }
  .big-text-detail-separator {
    padding: 0;
  }
  .big-text-detail-separator{
    div {
      width: 1px;
      height: 100%;
      margin: auto;
    }
  }
  .big-text-detail-value {
    font-size: 1rem;
    font-weight: 900;
  }
  .tranx-error-popup-view{
    text-align: center;
    .img-fluid{
      width: 50%;
      height: auto;
    }
  }

  .line-chart-time-filter{
    a {
      text-decoration: none;
      color: $border;
      font-size: $text;
      font-weight: 500;
    }
    .active {
      color: $orange;
    }
  }

  .info-icon-adjustment{
    position: relative;
    top:-3px;
  }
  .disable-buttons{
    position: absolute;
    right:0;
    top: 0;
    width: 8rem;
    bottom:0;
    cursor: not-allowed;
  }

  .info-hover-btn{
    margin-left: 5px;
    padding: 0;
  }

  .filter-icon{
    position: relative;
    top:-2px;
  }
  .filter-container{
    font-size: $text;
    color: $black;
    min-width: calc(20rem);
    
    @extend .box-shadow;
    .search-field{
      margin-bottom: 3px;
    }
    .form-check{
      .checkbox-resize{
        width: 1rem;
        height: 1rem;
        position: relative;
        top: 2px;
        left: 0px;
      }
    }
    .checklist-view{
      max-height: 10rem;
      overflow: auto;
    }
  }

  // Small devices (landscape phones, 576px and up)
@media (min-width: 0) and (max-width: 575px) { 
  .table-size{
    min-width: 40rem !important;
  }
  .section-heading{
    font-size: $normal-text;
    font-weight: 700;
    line-height: 1;
  }
 }
  // Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767px) { 
  .table-size{
    min-width: 40rem !important;
  }
 }

